import React, { CSSProperties, useEffect, useState } from 'react';
import { useSelector, useDispatch, Dispatch, connect } from 'react-redux';
import { cLog } from '@debug/logger';
import Styles from '@assets/scss/TableSheet.module.scss';

import { Fields, MouseEventMap, SheetModifyData, SheetViewPort, StoreState } from '@dataTypes';
import FieldView, { FIELD_TYPE } from './field-view';
import config from '@sheet/config';

const { TABLE_SHEET_SIZE, SHEET_BOX_PADDING } = config.SHEET_TABLE;
const { BORDER_SIZE, TOP_FIELD_HEIGHT } = TABLE_SHEET_SIZE;
const { BOTTOM } = SHEET_BOX_PADDING;

export type LeftCellGroupProps = {
    fields: Fields;
    modifyData: SheetModifyData;
    cellHeight?: number;
    viewPort?: SheetViewPort;
    fieldMouseEventMap?: MouseEventMap;
    updateTop?: (e) => void;
};

class LeftCellGroup extends React.Component<LeftCellGroupProps> {
    state = {};

    leftCellGroupRef: React.RefObject<HTMLDivElement>;

    _isSynchronizationTop: boolean = false;

    constructor(props) {
        super(props);

        this.leftCellGroupRef = React.createRef();
    }

    componentWillUnmount() {
        cLog('[LeftCellGroup] componentWillUnmount');
        this.leftCellGroupRef.current?.removeEventListener('scroll', this._onScroll);
    }

    componentDidMount() {
        cLog('[LeftCellGroup] componentDidMount');
        this.leftCellGroupRef.current?.addEventListener('scroll', this._onScroll);
    }

    componentDidUpdate() {
        cLog('[LeftCellGroup] componentDidUpdate');
    }

    synchronizationTop = (top, target) => {
        if (!this.leftCellGroupRef.current) return;
        if (!target || this.leftCellGroupRef.current !== target) {
            this._isSynchronizationTop = true;
            this.leftCellGroupRef.current.scrollTop = top;
        }
    };

    synchronizationScroll = (_left, top) => {
        if (!this.leftCellGroupRef.current) return;
        this._isSynchronizationTop = true;
        this.leftCellGroupRef.current.scrollTop = top;
    };

    _onScroll = (e) => {
        cLog('[LeftCellGroup] _onScroll', this._isSynchronizationTop);
        if (this._isSynchronizationTop) {
            this._isSynchronizationTop = false;
            return;
        }

        const { updateTop } = this.props;
        if (updateTop) updateTop(e);
    };

    render() {
        const { fields, modifyData, viewPort, fieldMouseEventMap, cellHeight } = this.props;
        const { cols } = fields || {};
        const { rootSize } = viewPort || {};
        const { height } = rootSize || {};
        let leftCellGroupStyle;

        if (cellHeight && height && height - TOP_FIELD_HEIGHT - BOTTOM > cellHeight) {
            leftCellGroupStyle = { height: cellHeight + BORDER_SIZE };
        }

        const fieldStyle: CSSProperties = { height: cellHeight };

        return (
            <div className={Styles.left_cell_group} ref={this.leftCellGroupRef} style={leftCellGroupStyle}>
                <FieldView
                    addStyle={fieldStyle}
                    className={Styles.left_cell}
                    type={FIELD_TYPE.LEFT}
                    fieldDatas={cols}
                    viewPort={viewPort}
                    modifyData={modifyData}
                    mouseEventMap={fieldMouseEventMap}
                />
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({
    // viewPort: state.editor.viewPort,
});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // selectCell: (selectCellItem) => dispatch(selectCell(selectCellItem)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(LeftCellGroup);
