/* eslint-disable react/no-direct-mutation-state */
/* eslint-disable react/no-deprecated */
import React from 'react';
import ReactDOM from 'react-dom';
import { Dispatch, connect } from 'react-redux';
import { SheetViewPort, StoreState } from '@dataTypes';
import { EditData } from '@sheet/model/edit-data';
import Styles from '@assets/scss/TableSheet.module.scss';
import CellEdit from './cell-edit';
import CellHide from './cell-hide';

import { getCellPositionCSS, getRowLength } from '@sheet/utils';
import { sheetActions } from '@sheet/reducers/sheet-reducer';
import { SheetItem } from '@sheet/model';
import config from '@sheet/config';
import SelectCellHook from '@sheet/hooks/select-cell-hook';
import ClickCountHook from '@sheet/hooks/click-count-hook';
import ModifyDataHook from '@sheet/hooks/modify-data-hook';
import RealViewPortHook from '@sheet/hooks/real-view-port-hook';
import { cLog } from '@debug/logger';
import { CSSProperties } from 'styled-components';
const { modifySheetData } = sheetActions;

const { CELL_WIDTH } = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const HIDE_PADDING = config.EDITOR.HIDE_PADDING;

type CellEditLayerProps = {
    editData?: EditData;
    modifySheetData: (sheetData) => void;
};

class CellEditLayer extends React.Component<CellEditLayerProps> {
    cellEditRef: React.RefObject<typeof CellEdit>;
    cellHideRef: React.RefObject<typeof CellHide>;
    cellEditBoxRef: React.RefObject<HTMLDivElement>;
    cellHideEditBoxRef: React.RefObject<HTMLDivElement>;

    state = {};

    _cellHideStyle = {
        maxWidth: '1000px',
    };

    _realViewPort?: SheetViewPort;

    // eslint-disable-next-line no-useless-constructor
    constructor(props: CellEditLayerProps) {
        super(props);

        this.cellEditRef = React.createRef();
        this.cellHideRef = React.createRef();
        this.cellEditBoxRef = React.createRef();
        this.cellHideEditBoxRef = React.createRef();
    }

    componentWillUnmount() {}

    componentWillMount() {
        cLog('[CellEditLayer] componentWillMount');
    }

    componentDidMount() {}

    componentDidUpdate() {
        cLog('[CellEditLayer] componentDidUpdate');
    }

    // eslint-disable-next-line react/no-deprecated
    componentWillReceiveProps(nextProps: CellEditLayerProps) {
        cLog('[CellEditLayer] componentWillReceiveProps');

        if (this.props.editData) {
            if (nextProps.editData) {
                const { editItem } = this.props.editData;
                const { isCancel } = nextProps.editData;
                if (isCancel && editItem && this.cellEditRef.current) {
                    const { str } = editItem;
                    this.cellEditRef.current.cancelValue(str);
                }
            } else {
                const { editItem } = this.props.editData;
                if (editItem && this.cellEditRef.current) {
                    const { col, row, str } = editItem;
                    const value = this.cellEditRef.current.getValue();

                    const { modifySheetData } = this.props;
                    if (modifySheetData) {
                        const sheetData = new SheetItem({
                            col,
                            row,
                            str: value,
                        });

                        modifySheetData(sheetData);
                    }
                }
            }
        }
    }

    _updateSize = (size: { width: number; height: number }, text?: string | number) => {
        if (this.cellEditRef.current) this.cellEditRef.current.updateSize(size, text);
    };

    _updateText = (text?: string) => {
        if (this.cellHideRef.current) this.cellHideRef.current.updateText(text);
    };

    _onChangeClickCount = (_clickCount) => {
        this.cellEditRef.current.focus(false);
    };

    _onChangeModifyData = (modifyData) => {
        if (!modifyData) this.cellEditRef.current.focus(false);
    };

    _onChangeRealViewPort = (realViewPort) => {
        this._realViewPort = realViewPort;
        this._updateRealViewPort();
    };

    _onChangeSelectCell = (_selectCell) => {
        this.cellEditRef.current.focus();
    };

    _updateRealViewPort = () => {
        const { editData } = this.props;
        if (!editData && this.cellEditBoxRef.current && this.cellHideEditBoxRef.current) {
            const { x, y } = this._realViewPort || {};
            const top = `${y}px`;
            const left = `${x}px`;
            (this.cellEditBoxRef.current.style as any).top = top;
            (this.cellEditBoxRef.current.style as any).left = left;
            (this.cellHideEditBoxRef.current.style as any).top = top;
            (this.cellHideEditBoxRef.current.style as any).left = left;
        }
    };

    render() {
        const { editData } = this.props;
        let position: CSSProperties = {};
        let defaultValue: string | number = '';
        let cellHideStyle;
        let defaultCursor;

        if (editData) {
            const { defaultText, editItem, cursor } = editData;
            if (editItem) {
                const { row, str } = editItem;

                defaultCursor = cursor;
                defaultValue = defaultText === undefined ? (str === undefined ? '' : str) : defaultText;
                position = getCellPositionCSS(editItem, -1, -1);
                position.zIndex = 100;

                position.zIndex = 100;

                const maxWidth = `${
                    (getRowLength() - (row || 0)) * CELL_WIDTH - (HIDE_PADDING.LEFT + HIDE_PADDING.RIGHT)
                }px`;
                cLog(maxWidth);

                cellHideStyle = {
                    ...this._cellHideStyle,
                    maxWidth,
                };
            }
        } else {
            if (this._realViewPort) {
                const { x, y } = this._realViewPort || {};
                position.top = `${y}px`;
                position.left = `${x}px`;
            }

            position.zIndex = -1000;
        }

        // return editData ? (
        //     <>
        //         <div className={Styles.cell_edit_box} style={position}>
        //             <CellEdit
        //                 ref={this.cellEditRef}
        //                 defaultCursor={defaultCursor}
        //                 defaultValue={defaultValue}
        //                 updateText={this._updateText}
        //             />
        //         </div>
        //         <div className={Styles.cell_edit_box}>
        //             <CellHide
        //                 ref={this.cellHideRef}
        //                 defaultValue={defaultValue}
        //                 position={cellHideStyle}
        //                 updateSize={this._updateSize}
        //             />
        //         </div>
        //     </>
        // ) : (
        //     <></>
        // );
        return (
            <>
                <RealViewPortHook changeRealViewPort={this._onChangeRealViewPort} />
                <ModifyDataHook changeModifyData={this._onChangeModifyData} />
                <ClickCountHook changeClickCount={this._onChangeClickCount} />
                <SelectCellHook changeSelectCell={this._onChangeSelectCell} />
                <div className={Styles.cell_edit_box} ref={this.cellEditBoxRef} style={position}>
                    <CellEdit
                        ref={this.cellEditRef}
                        defaultCursor={defaultCursor}
                        defaultValue={defaultValue}
                        updateText={this._updateText}
                    />
                </div>
                <div className={Styles.cell_edit_box} ref={this.cellHideEditBoxRef} style={position}>
                    <CellHide
                        ref={this.cellHideRef}
                        defaultValue={defaultValue}
                        position={cellHideStyle}
                        updateSize={this._updateSize}
                    />
                </div>
            </>
        );
    }
}

const mapStateToProps = (state: StoreState) => ({
    editData: state.editor.editData,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    modifySheetData: (modifyCellItem) => dispatch(modifySheetData(modifyCellItem)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(CellEditLayer);
