import { applyMiddleware, createStore, Store } from 'redux';
import { createLogger } from 'redux-logger';
import reducer from '@sheet/reducers/index';
import { StoreState } from '@dataTypes';
import config from '@sheet/config';

const isLog = config.DEBUG.useLog;
const logger: any = isLog && createLogger();

class StoreManager {
    private _store: Store<StoreState>;

    constructor() {
        this._store = isLog ? createStore(reducer, applyMiddleware(logger)) : createStore(reducer);
    }

    public get store(): Store<StoreState> {
        return this._store;
    }

    public get state(): StoreState {
        return this._store.getState();
    }
}

export default new StoreManager();
