const selectFieldModifyData = (state: StoreState) => state.sheet.fieldModifyData;

export enum FIELD_TYPE {
    TOP = 'fieldTop',
    TOP_GROUP = 'fieldTopGroup',
    LEFT = 'fieldLeft',
    LEFT_GROUP = 'fieldLeftGroup',
}

const nextFieldItems = (props, items) => {
    const { MAX_RENDER } = config.SHEET_TABLE;
    const { fieldDatas } = props;
    const startIndex = items.length;
    if (!fieldDatas || startIndex === fieldDatas.length) return null;
    const nextCount = fieldDatas.length - startIndex;
    const len = nextCount > MAX_RENDER ? startIndex + MAX_RENDER : fieldDatas.length;
    const fieldItems = fieldDatas.slice(startIndex, len);

    return fieldItems;
};

type FieldProps = {
    viewPort?: SheetViewPort;
    fieldDatas?: FieldItem[];
    modifyData?: SheetModifyData;
    mouseEventMap?: MouseEventMap;
};

