export type FieldViewProps = {
    type: string;
    className: string;
    addStyle?: CSSProperties;
    viewPort?: SheetViewPort;
    fieldDatas?: FieldItem[];
    modifyData?: SheetModifyData;
    mouseEventMap?: MouseEventMap;
};

class FieldView extends React.Component<FieldViewProps> {
    state = {};

    fieldContainerRef: React.RefObject<HTMLDivElement>;

    constructor(props) {
        super(props);

        this.fieldContainerRef = React.createRef();
    }

    componentWillUnmount() {
        cLog('[FieldView] componentWillUnmount');
    }

    componentDidMount() {
        cLog('[FieldView] componentDidMount');
    }

    componentDidUpdate() {
        cLog('[FieldView] componentDidUpdate');
    }

    updateDevicePixelRatio = () => {
        const { fieldDatas } = this.props;
        const width = BORDER_SIZE + CELL_WIDTH * (fieldDatas?.length || 1) + getDevicePixelRatio();

        if (this.fieldContainerRef.current?.style) this.fieldContainerRef.current.style.width = `${width}px`;
    };

    render() {
        const { fieldDatas, modifyData, mouseEventMap, viewPort, addStyle } = this.props;

        return (
            <div className={this.props.className} ref={this.fieldContainerRef} style={addStyle}>
                {this.props.type === FIELD_TYPE.TOP || this.props.type === FIELD_TYPE.TOP_GROUP ? (
                    <TopItems
                        fieldDatas={fieldDatas}
                        viewPort={viewPort}
                        modifyData={modifyData}
                        mouseEventMap={mouseEventMap}
                    />
                ) : (
                    <LeftItems
                        fieldDatas={fieldDatas}
                        viewPort={viewPort}
                        modifyData={modifyData}
                        mouseEventMap={mouseEventMap}
                    />
                )}
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({
    // modifyData: state.editor.modifyData,
});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // addEditorModifyData: (eModifyData) => dispatch(editorModifyData(eModifyData)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(FieldView);
