import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectRealViewPort = (state: StoreState) => state.editor.realViewPort;

type RealViewPortHookProps = {
    changeRealViewPort?: (realViewPort) => void;
};

const RealViewPortHook = (props: RealViewPortHookProps) => {
    const realViewPort = useSelector(selectRealViewPort);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeRealViewPort } = props || {};
        if (changeRealViewPort) changeRealViewPort(realViewPort);
    }, [realViewPort]);

    return <></>;
};

export default RealViewPortHook;
