import React, { useState, useEffect, useRef, useCallback, forwardRef } from 'react';
import Styles from '@assets/scss/TableSheet.module.scss';
import { useSelector } from 'react-redux';

declare let ResizeObserver: {
    prototype: ResizeObserver;
    new (callback: ResizeObserverCallback): ResizeObserver;
};

declare interface ResizeObserver {
    observe(target: Element): void;
    unobserve(target: Element): void;
    disconnect(): void;
}

interface ResizeObserverSize {
    inlineSize: number;
    blockSize: number;
}

type ResizeObserverCallback = (entries: ReadonlyArray<ResizeObserverEntry>, observer: ResizeObserver) => void;

interface ResizeObserverEntry {
    readonly target: Element;
    readonly contentRect: DOMRectReadOnly;
    readonly borderBoxSize: ResizeObserverSize;
    readonly contentBoxSize: ResizeObserverSize;
}

export type ScrollBarProps = {
    children: React.ReactNode;
    className?: String;
    style?: any;
    onScroll?: any;
};

// eslint-disable-next-line react/display-name
const Scrollbar = forwardRef((props: ScrollBarProps, ref: React.ForwardedRef<HTMLDivElement>) => {
    const { children, className, style, onScroll } = props;
    const contentRef = useRef<HTMLDivElement | null>(null);
    const scrollTrackRef = useRef<HTMLDivElement>(null);
    const scrollThumbRef = useRef<HTMLDivElement>(null);
    const scrollTrandAndThumb = useRef<HTMLDivElement>(null);
    const observer = useRef<ResizeObserver | null>(null);
    const [thumbHeight, setThumbHeight] = useState(20);
    const [scrollStartPosition, setScrollStartPosition] = useState<number>(0);
    const [initialScrollTop, setInitialScrollTop] = useState<number>(0);
    const [isDragging, setIsDragging] = useState(false);
    const [showScroll, setShowScroll] = useState(true);
    const colCount = useSelector((state) => {
        return state?.sheet?.fields?.cols.length;
    });

    function handleResize(ref: HTMLDivElement, trackSize: number) {
        const { clientHeight, scrollHeight } = ref;
        if (clientHeight >= scrollHeight) {
            setShowScroll(false);
        }
        setThumbHeight(Math.max((clientHeight / scrollHeight) * trackSize, 20));
    }

    const handleTrackClick = useCallback(
        (e) => {
            e.preventDefault();
            e.stopPropagation();
            const { current: trackCurrent } = scrollTrackRef;
            const { current: contentCurrent } = contentRef;
            if (trackCurrent && contentCurrent) {
                const { clientY } = e;
                const target = e.target as HTMLDivElement;
                const rect = target.getBoundingClientRect();
                const trackTop = rect.top;
                const thumbOffset = -(thumbHeight / 2);
                const clickRatio = (clientY - trackTop + thumbOffset) / trackCurrent.clientHeight;
                const scrollAmount = Math.floor(clickRatio * contentCurrent.scrollHeight);
                contentCurrent.scrollTo({
                    top: scrollAmount,
                    behavior: 'smooth',
                });
            }
        },
        [thumbHeight],
    );

    const handleThumbPosition = useCallback((e) => {
        if (!contentRef.current || !scrollTrackRef.current || !scrollThumbRef.current) {
            return;
        }
        const { scrollTop: contentTop, scrollHeight: contentHeight } = contentRef.current;
        const { clientHeight: trackHeight } = scrollTrackRef.current;
        let newTop = (+contentTop / +contentHeight) * trackHeight;
        newTop = Math.min(newTop, trackHeight - thumbHeight);
        const thumb = scrollThumbRef.current;
        thumb.style.top = `${newTop}px`;
        if (scrollTrandAndThumb.current) {
            const top = e.target.scrollTop;
            scrollTrandAndThumb.current.style.top = `${top}px`;
        }
    }, []);

    const handleThumbMousedown = useCallback((e) => {
        e.preventDefault();
        e.stopPropagation();
        setScrollStartPosition(e.clientY);
        if (contentRef.current) setInitialScrollTop(contentRef.current.scrollTop);
        setIsDragging(true);
    }, []);

    const handleThumbMouseup = useCallback(
        (e) => {
            e.preventDefault();
            // e.stopPropagation();
            if (isDragging) {
                setIsDragging(false);
            }
        },
        [isDragging],
    );

    const handleThumbMousemove = useCallback(
        (e) => {
            e.preventDefault();
            e.stopPropagation();
            if (isDragging && contentRef.current) {
                const { scrollHeight: contentScrollHeight, offsetHeight: contentOffsetHeight } = contentRef.current;

                const deltaY = (e.clientY - scrollStartPosition) * (contentOffsetHeight / thumbHeight);
                const newScrollTop = Math.min(initialScrollTop + deltaY, contentScrollHeight - contentOffsetHeight);

                contentRef.current.scrollTop = newScrollTop;
            }
        },
        [isDragging, scrollStartPosition, thumbHeight],
    );

    // If the content and the scrollbar track exist, use a ResizeObserver to adjust height of thumb and listen for scroll event to move the thumb
    useEffect(() => {
        if (contentRef.current && scrollTrackRef.current) {
            const ref = contentRef.current;
            const { clientHeight: trackSize } = scrollTrackRef.current;
            observer.current = new ResizeObserver(() => {
                handleResize(ref, trackSize);
            });
            observer.current.observe(ref);
            ref.addEventListener('scroll', handleThumbPosition);
            return () => {
                observer.current?.unobserve(ref);
                ref.removeEventListener('scroll', handleThumbPosition);
            };
        }
        return undefined;
    }, []);

    useEffect(() => {
        if (contentRef.current && scrollTrackRef.current) {
            const ref = contentRef.current;
            const { clientHeight: trackSize } = scrollTrackRef.current;
            handleResize(ref, trackSize);
        }
    }, [colCount]);

    // Listen for mouse events to handle scrolling by dragging the thumb
    useEffect(() => {
        document.addEventListener('mousemove', handleThumbMousemove);
        document.addEventListener('mouseup', handleThumbMouseup);
        document.addEventListener('mouseleave', handleThumbMouseup);
        return () => {
            document.removeEventListener('mousemove', handleThumbMousemove);
            document.removeEventListener('mouseup', handleThumbMouseup);
            document.removeEventListener('mouseleave', handleThumbMouseup);
        };
    }, [handleThumbMousemove, handleThumbMouseup]);

    return (
        <div
            className={`${Styles.custom_scrollbars__container} ${className}`}
            style={style}
            ref={(node) => {
                contentRef.current = node;
                if (typeof ref === 'function') {
                    ref(node);
                } else if (ref) {
                    ref.current = node;
                }
            }}>
            {children}
            {showScroll && (
                <div className={Styles.custom_scrollbars__scrollbar}>
                    <div className={Styles.custom_scrollbars__track_and_thumb} ref={scrollTrandAndThumb}>
                        <div
                            className={Styles.custom_scrollbars__track}
                            ref={scrollTrackRef}
                            onClick={handleTrackClick}></div>
                        <div
                            className={Styles.custom_scrollbars__thumb}
                            ref={scrollThumbRef}
                            onMouseDown={handleThumbMousedown}
                            style={{
                                height: `${thumbHeight}px`,
                                cursor: isDragging ? 'grabbing' : 'grab',
                            }}></div>
                    </div>
                </div>
            )}
        </div>
    );
});

export default Scrollbar;
