/* eslint-disable react/no-deprecated */
import React, { useEffect } from 'react';
import { Dispatch, connect } from 'react-redux';
import { IBaseItem, StoreState, SheetModifyRect, SheetData, SheetOption } from '@dataTypes';
import Styles from '@assets/scss/TableSheet.module.scss';
import SheetBoxObserver, { SheetBoxObserverMap } from './sheet-box-observer';
import { editorActions } from '@sheet/reducers/editor-reducer';
import { clearRectData, copyRectData, getClipboardData, getColLength, getRowLength, getSaveData } from '@sheet/utils';
import StoreManager from '@sheet/helper/store-manager';
import { EditData } from '@sheet/model/edit-data';
import { EditorModifyData, EDITOR_MODIFY_TYPE } from '@sheet/model/editor-modify-data';
import SheetBoxResizeSensor from './sheet-box-resize-sensor';
import config from '@sheet/config';
import CellSaveLayer from '../sheet-ui/cell-save-layer';
import ErrorDimmed from '../sheet-ui/error-dimmed';
import { SheetItem } from '@sheet/model';
import { cLog, getDebugTime } from '@debug/logger';
import SheetTableContainer from '@sheet/container/sheet-table-container';
import SheetOptionContainer from '@sheet/container/sheet-option-container';
import { MODIFY_RECT_TYPE } from '@sheet/model/modify-rect-type';
import { MODIFY_TYPE, ModifyData } from '@sheet/model/modify-data';
import { sheetActions } from '@sheet/reducers/sheet-reducer';

const { pasteCell, deleteCell } = sheetActions;

const {
    selectCell,
    selectEndCell,
    modifyRect,
    setEdit,
    editorModifyData,
    setViewPort,
    setRealViewPort,
    addClickCount,
} = editorActions;

const {
    TOP: BOX_PADDING_TOP,
    LEFT: BOX_PADDING_LEFT,
    RIGHT: BOX_PADDING_RIGHT,
    BOTTOM: BOX_PADDING_BOTTOM,
} = config.SHEET_TABLE.SHEET_BOX_PADDING;

const { FIRST_CELL_SIZE, CELL_WIDTH, CELL_HEIGHT, TOP_FIELD_HEIGHT, BORDER_SIZE } = config.SHEET_TABLE.TABLE_SHEET_SIZE;

export type SheetBoxProps = {
    children?: any;
    sheetData?: SheetData;
    option?: SheetOption;

    selectCell: (selectCellItem) => void;
    selectEndCell: (selectCellItem) => void;
    setEdit: (editorData) => void;
    addModifyRect: (sheetModifyRect) => void;
    addEditorModifyData: (editorModifyData) => void;
    addViewPort: (viewPort) => void;
    addRealViewPort: (viewPort) => void;
    addClickCountPlus: () => void;

    pasteCell: (modifyCell) => void;
    deleteCell: (modifyCell) => void;
};

class SheetBox extends React.Component<SheetBoxProps> {
    state = {};

    sheetBoxRef: React.RefObject<HTMLDivElement>;
    innerRef: React.RefObject<HTMLDivElement>;
    sheetTableContainerRef: React.RefObject<typeof SheetTableContainer>;

    isFocus = false;
    isDown = false;

    _isEnterUp = false;
    _isTabUp = false;

    _sheetBoxObserverMap: SheetBoxObserverMap;

    _selectCell?: IBaseItem;
    _editData?: EditData;
    _modifyRect?: SheetModifyRect;
    _modifyData?: EditorModifyData;

    _tabPostion?: { col?: number; row?: number };
    _viewPort?: { x: number; y: number; width: number; height: number; rect?: SheetModifyRect };
    _localViewPort: {
        x: number;
        y: number;
        width: number;
        height: number;
        devicePixelRatio: number;
        rootSize: { width: number; height: number };
        viewPortMaxSize: { width: number; height: number };
    };

    constructor(props) {
        super(props);

        this.sheetBoxRef = React.createRef();
        this.innerRef = React.createRef();
        this.sheetTableContainerRef = React.createRef();

        this._sheetBoxObserverMap = {
            changeSelectCell: this._changeSelectCell,
            changeEditData: this._changeEditData,
            changeModifyRect: this._changeModifyRect,
            changeModifyData: this._changeModifyData,
            changeViewPort: this._changeViewPort,
        };

        this._localViewPort = {
            x: 0,
            y: 0,
            width: 0,
            height: 0,
            devicePixelRatio: 1,
            rootSize: { width: 0, height: 0 },
            viewPortMaxSize: { width: 0, height: 0 },
        };
    }

    componentWillUnmount() {
        cLog('[SheetBox] componentWillUnmount');
        this.sheetBoxRef.current?.removeEventListener('mousedown', this._onClick);

        window.removeEventListener('mousedown', this._onWindowClick);
        window.removeEventListener('keydown', this._onKeyDown);
        window.removeEventListener('keyup', this._onKeyUp);
    }

    componentWillMount() {
        cLog('[SheetBox] componentWillMount');
    }

    componentDidMount() {
        cLog('[SheetBox] componentDidMount');
        this.sheetBoxRef.current?.addEventListener('mousedown', this._onClick);

        window.addEventListener('mousedown', this._onWindowClick);
        window.addEventListener('keydown', this._onKeyDown);
        window.addEventListener('keyup', this._onKeyUp);

        this._updateViewPort();
    }

    componentDidUpdate() {
        cLog('[SheetBox] componentDidUpdate');
    }

    _changeSelectCell = (selectCell: IBaseItem) => {
        this._selectCell = selectCell;
        if (!this._getIsKeyBord()) this._tabPostion = undefined;

        if (this._selectCell && this.sheetTableContainerRef.current) {
            const { col, row } = this._selectCell;
            const left = (row || 0) * CELL_WIDTH;
            const top = (col || 0) * CELL_HEIGHT;
            const cellRect = {
                left,
                top,
                right: left + CELL_WIDTH,
                bottom: top + CELL_HEIGHT,
            };

            let isChange = false;

            let updateLeft = this._localViewPort.x;
            let updateTop = this._localViewPort.y;

            if (cellRect.left < this._localViewPort.x) {
                updateLeft = cellRect.left;
                isChange = true;
            } else if (cellRect.right > this._localViewPort.x + this._localViewPort.width) {
                updateLeft = cellRect.right - this._localViewPort.width + 1;
                isChange = true;
            }

            if (cellRect.top < this._localViewPort.y) {
                updateTop = cellRect.top - 1;
                if (updateTop < 0) updateTop = 0;
                isChange = true;
            } else if (cellRect.bottom > this._localViewPort.y + this._localViewPort.height) {
                updateTop = cellRect.bottom - this._localViewPort.height;
                isChange = true;
            }

            if (isChange) {
                this.sheetTableContainerRef.current.updateScroll(updateLeft, updateTop);
                setTimeout(() => this._updateViewPort(), 0);
            }
        }
    };

    _changeModifyRect = (modifyRect) => {
        this._modifyRect = modifyRect;
    };

    _changeModifyData = (modifyData) => {
        this._modifyData = modifyData;
    };

    _changeViewPort = (viewPort) => {
        this._viewPort = viewPort;
    };

    _changeEditData = (editData?: EditData) => {
        this._editData = editData;
        if (this._editData) {
            const { isCancel } = this._editData;
            if (isCancel && this._selectCell) {
                const { col, row } = this._selectCell;
                this._setSelectCell(col, row);
            }
        }
    };

    _setSelectCell = (col, row, isKeyBord?: boolean) => {
        const { selectCell } = this.props;
        if (selectCell) {
            const sheetItem = this._getSheetItem(col, row);
            selectCell({ selectCell: sheetItem, isKeyBord });
        }
    };

    _setSelectEndCell = (col, row, isKeyBord?: boolean) => {
        const { selectEndCell } = this.props;
        if (selectEndCell) {
            selectEndCell({ col, row, isKeyBord });
        }
    };

    _getIsKeyBord = () => StoreManager.state.editor.isKeyBord;

    _getSheetItem = (col, row) => {
        const { sheetItems } = StoreManager.state.sheet;
        return sheetItems ? sheetItems[col][row] : undefined;
    };

    _enterKey = (e) => {
        const { shiftKey, altKey, target } = e;
        if (!this._selectCell) return;

        if (altKey) {
            const cursorPosition = target.selectionStart;
            const text = target.value;
            target.value = text.slice(0, cursorPosition) + '\n' + text.slice(cursorPosition);
            target.selectionEnd = cursorPosition + 1;
            target.scrollTop = target.scrollHeight;
        } else if (!this._isOptionOpen()) {
            const { col, row } = this._selectCell;
            const colLen = getColLength();
            let nextCol = col || 0;
            let nextRow = row || 0;

            if (this._tabPostion) {
                nextCol = this._tabPostion.col || 0;
                nextRow = this._tabPostion.row || 0;
            }

            if (shiftKey) {
                nextCol -= 1;
            } else {
                nextCol += 1;
            }
            if (nextCol < colLen && nextCol >= 0) {
                if (!this._tabPostion) this._tabPostion = {};
                this._tabPostion.col = nextCol;
                this._tabPostion.row = nextRow;
                this._setSelectCell(nextCol, nextRow, true);
            }
        }
    };

    _escKey = (e) => {
        cLog('Esc');
        const { addModifyRect } = this.props;

        // edit Cancel
        this._cancelEdit(e);

        // / cancel
        if (addModifyRect && this._modifyRect) {
            e.preventDefault();
            addModifyRect(undefined);
        }

        this._cancelOption(e);
    };

    _cancelEdit = (e?) => {
        const { setEdit } = this.props;
        if (setEdit && this._editData) {
            if (e) e.preventDefault();
            setEdit({ cursor: undefined, isCancel: true });
        }
    };

    _completeEdit = () => {
        if (this._editData && this._selectCell) {
            const { col, row } = this._selectCell;
            this._setSelectCell(col, row);
        }
    };

    _cancelOption = (e?) => {
        if (this._modifyData) {
            const { addEditorModifyData } = this.props;
            if (addEditorModifyData) {
                if (e) e.preventDefault();
                const { type, openOption } = this._modifyData;
                if (type === EDITOR_MODIFY_TYPE.FIELD) {
                    if (openOption) addEditorModifyData(undefined);
                } else {
                    addEditorModifyData(undefined);
                }
            }
        }
    };

    _isOptionOpen = () => {
        let isOptionOpen = true;
        if (this._modifyData) {
            const { type, openOption } = this._modifyData;
            if (type === EDITOR_MODIFY_TYPE.FIELD && !openOption) {
                isOptionOpen = false;
            }
        } else {
            isOptionOpen = false;
        }

        return isOptionOpen;
    };

    _deleteKey = (_e) => {
        const { deleteCell, addEditorModifyData, addModifyRect } = this.props;
        let modifyData: ModifyData | null = new ModifyData();
        modifyData.type = MODIFY_TYPE.DELETE;
        const { rect, selectCell } = StoreManager.state.editor;

        if (rect) {
            const { sc, ec, sr, er } = rect;
            modifyData.start = { col: sc, row: sr };
            modifyData.end = { col: ec, row: er };
        } else if (selectCell) {
            modifyData.start = selectCell;
            modifyData.end = selectCell;
        } else {
            modifyData = null;
        }

        if (modifyData) {
            addEditorModifyData(undefined);
            deleteCell(modifyData);
        }

        addModifyRect(undefined);
    };

    _handleShortcut = async (e, isCtrlOrCmd: boolean) => {
        const { addModifyRect, addEditorModifyData, pasteCell, deleteCell } = this.props;
        const { key } = e;

        if (isCtrlOrCmd && key === 'c') {
            copyRectData();
            addModifyRect(MODIFY_RECT_TYPE.COPY);
            addEditorModifyData(undefined);
        } else if (isCtrlOrCmd && key === 'x') {
            copyRectData();
            addModifyRect(MODIFY_RECT_TYPE.CUT);
            addEditorModifyData(undefined);
        } else if (isCtrlOrCmd && key === 'v') {
            const clipboardData = await getClipboardData();

            if (clipboardData) {
                addModifyRect(undefined);
                if (this._modifyRect?.type === MODIFY_RECT_TYPE.CUT) {
                    const modifyData: ModifyData | null = new ModifyData();
                    modifyData.type = MODIFY_TYPE.DELETE;
                    modifyData.start = { col: this._modifyRect.sc, row: this._modifyRect.sr };
                    modifyData.end = { col: this._modifyRect.ec, row: this._modifyRect.er };
                    deleteCell(modifyData);
                    clearRectData();
                }
                pasteCell(clipboardData);
                addEditorModifyData(undefined);
            }
        }
    };

    _setEditKey = (e) => {
        const { key } = e;
        // key  1      .
        if ((key.length === 1 || key === 'Process') && !this._editData) {
            const { setEdit } = this.props;
            if (setEdit && !this._isOptionOpen()) {
                e.preventDefault();

                // window  key  process ....
                if (key === 'Process') {
                    setEdit({ defaultText: '', cursor: undefined });
                    return;
                }

                const check_num = /[0-9]/;
                const check_eng = /[a-zA-Z]/;
                const check_spc = /[~!@#$%^&*()_+|<>?:{}]/;

                if (check_num.test(key) || check_eng.test(key) || check_spc.test(key)) {
                    setEdit({ defaultText: key, cursor: undefined });
                } else {
                    setEdit({ defaultText: '', cursor: undefined });
                }
                // setEdit({ defaultText: key, cursor: undefined });
                // setEdit({ defaultText: '', cursor: undefined });
            }
        }
    };

    _onKeyUp = (e) => {
        const { key } = e;
        if (key === 'Enter') {
            this._isEnterUp = true;
            setTimeout(() => {
                this._isEnterUp = false;
            }, 0);
        } else if (key === 'Tab') {
            this._isTabUp = true;
            setTimeout(() => {
                this._isTabUp = false;
            }, 0);
        }
    };

    _onKeyDown = (e) => {
        if (!this.isFocus || !this._selectCell) return;

        const { col, row } = this._selectCell;
        const { key, shiftKey } = e;

        let rowLen;
        let colLen;
        let nextRow;
        let nextCol;

        switch (key) {
            case 'Tab':
                e.preventDefault();
                if (!this._isOptionOpen()) {
                    if (this._isTabUp) return;

                    rowLen = getRowLength();
                    if (shiftKey) {
                        nextRow = (row || 0) - 1;
                    } else {
                        nextRow = (row || 0) + 1;
                    }
                    nextCol = col || 0;
                    if (nextRow < rowLen && nextRow >= 0) {
                        if (!this._getIsKeyBord()) {
                            this._tabPostion = {};
                            this._tabPostion.col = col;
                            this._tabPostion.row = row;
                        }

                        this._setSelectCell(nextCol, nextRow, true);
                    }
                }

                break;
            case 'Down':
            case 'ArrowDown':
                if (!this._editData) {
                    e.preventDefault();
                    if (!this._isOptionOpen()) {
                        colLen = getColLength();
                        nextRow = row || 0;
                        nextCol = (col || 0) + 1;
                        if (shiftKey) {
                            const endCell = StoreManager.state.editor.endCell;
                            if (endCell) {
                                nextRow = endCell.row || 0;
                                nextCol = (endCell.col || 0) + 1;
                            }
                            if (nextCol < colLen) {
                                this._setSelectEndCell(nextCol, nextRow);
                            }
                        } else if (nextCol < colLen) {
                            this._setSelectCell(nextCol, nextRow);
                        }
                    }
                }
                break;
            case 'Up':
            case 'ArrowUp':
                if (!this._editData) {
                    e.preventDefault();
                    if (!this._isOptionOpen()) {
                        nextRow = row || 0;
                        nextCol = (col || 0) - 1;
                        if (shiftKey) {
                            const endCell = StoreManager.state.editor.endCell;
                            if (endCell) {
                                nextRow = endCell.row || 0;
                                nextCol = (endCell.col || 0) - 1;
                            }
                            if (nextCol > -1) {
                                this._setSelectEndCell(nextCol, nextRow);
                            }
                        } else if (nextCol > -1) {
                            this._setSelectCell(nextCol, nextRow);
                        }
                    }
                }

                break;
            case 'Left':
            case 'ArrowLeft':
                if (!this._editData) {
                    e.preventDefault();
                    if (!this._isOptionOpen()) {
                        nextRow = (row || 0) - 1;
                        nextCol = col || 0;
                        if (shiftKey) {
                            const endCell = StoreManager.state.editor.endCell;
                            if (endCell) {
                                nextRow = (endCell.row || 0) - 1;
                                nextCol = endCell.col || 0;
                            }
                            if (nextRow > -1) {
                                this._setSelectEndCell(nextCol, nextRow);
                            }
                        } else if (nextRow > -1) {
                            this._setSelectCell(nextCol, nextRow);
                        }
                    }
                }

                break;
            case 'Right':
            case 'ArrowRight':
                if (!this._editData) {
                    e.preventDefault();
                    if (!this._isOptionOpen()) {
                        rowLen = getRowLength();
                        nextRow = (row || 0) + 1;
                        nextCol = col || 0;
                        if (shiftKey) {
                            const endCell = StoreManager.state.editor.endCell;
                            if (endCell) {
                                nextRow = (endCell.row || 0) + 1;
                                nextCol = endCell.col || 0;
                            }
                            if (nextRow < rowLen) {
                                this._setSelectEndCell(nextCol, nextRow);
                            }
                        } else if (nextRow < rowLen) {
                            this._setSelectCell(nextCol, nextRow);
                        }
                    }
                }

                break;
            case 'Enter':
                e.preventDefault();
                if (this._isEnterUp) return;
                this._enterKey(e);
                break;
            case 'Esc':
            case 'Escape':
                this._escKey(e);
                break;
            case 'Delete': {
                this._deleteKey(e);
                break;
            }
            default:
                const isMac = /Mac/i.test(navigator.userAgent);
                const isCtrlOrCmd = isMac ? e.metaKey : e.ctrlKey;
                if (!isCtrlOrCmd) {
                    this._setEditKey(e);
                    return;
                }
                if (['c', 'x', 'v'].indexOf(key) >= 0) {
                    this._handleShortcut(e, isCtrlOrCmd);
                }
                return;
        }
    };

    _onResize = () => {
        this._updateViewPort();
    };

    _onClick = (_e) => {
        cLog('_onClick');
        this.isDown = true;
    };

    _onWindowClick = (_e) => {
        cLog('_onWindowClick');
        this.isFocus = this.isDown;
        this.isDown = false;

        if (!this.isFocus) {
            // this._cancelEdit();
            this._completeEdit();
            this._cancelOption();
        } else {
            const { addClickCountPlus } = this.props;
            if (addClickCountPlus) addClickCountPlus();
        }
    };

    _updateViewPort = () => {
        if (
            this._modifyData &&
            (this._modifyData.type === EDITOR_MODIFY_TYPE.FIELD || this._modifyData.type === EDITOR_MODIFY_TYPE.CELL)
        ) {
            const { addEditorModifyData } = this.props;
            if (addEditorModifyData) addEditorModifyData(undefined);
        }

        if (this.sheetTableContainerRef.current) {
            let rootWidth = 0;
            let rootHeight = 0;
            if (this.sheetBoxRef.current) {
                rootWidth = this.sheetBoxRef.current.clientWidth;
                rootHeight = this.sheetBoxRef.current.clientHeight;
            }

            const colLastIndex = getColLength() - 1;
            const rowLastIndex = getRowLength() - 1;
            const { x, y, width, height } = this.sheetTableContainerRef.current.getRect();
            const sx = x;
            const sy = y;
            const sr = (sx < 0 ? 0 : sx / CELL_WIDTH) | 0;
            const sc = (sy < 0 ? 0 : sy / CELL_HEIGHT) | 0;

            const viewPortMaxSize = {
                width,
                height: rootHeight - TOP_FIELD_HEIGHT - BOX_PADDING_BOTTOM,
            };

            const ex = sx + width;
            const ey = sy + height;
            let er = (ex / CELL_WIDTH) | 0;
            let ec = (ey / CELL_HEIGHT) | 0;

            if (ec > colLastIndex) ec = colLastIndex;
            if (er > rowLastIndex) er = rowLastIndex;

            if (this._localViewPort.devicePixelRatio !== window.devicePixelRatio) {
                this.sheetTableContainerRef.current.updateDevicePixelRatio();
            }

            this._localViewPort.x = x;
            this._localViewPort.y = y;
            this._localViewPort.width = width;
            this._localViewPort.height = height;
            this._localViewPort.devicePixelRatio = window.devicePixelRatio;
            this._localViewPort.rootSize = { width: rootWidth, height: rootHeight };
            this._localViewPort.viewPortMaxSize = viewPortMaxSize;
            const { addViewPort, addRealViewPort } = this.props;

            if (!this._viewPort) {
                if (addViewPort)
                    addViewPort({
                        x,
                        y,
                        width,
                        height,
                        rect: { sc, ec, sr, er },
                        devicePixelRatio: window.devicePixelRatio,
                        rootSize: {
                            width: rootWidth,
                            height: rootHeight,
                        },
                        viewPortMaxSize,
                    });
            } else {
                const { rect } = this._viewPort;
                const { sr: osr, sc: osc, er: oer, ec: oec } = rect || {};
                if (osr !== sr || osc !== sc || oer !== er || oec !== ec) {
                    //     
                    if (addViewPort)
                        setTimeout(
                            () =>
                                addViewPort({
                                    x,
                                    y,
                                    width,
                                    height,
                                    rect: { sc, ec, sr, er },
                                    devicePixelRatio: window.devicePixelRatio,
                                    rootSize: {
                                        width: rootWidth,
                                        height: rootHeight,
                                    },
                                    viewPortMaxSize,
                                }),
                            0,
                        );
                }
            }

            addRealViewPort({
                x,
                y,
                width,
                height,
                rect: { sc, ec, sr, er },
                devicePixelRatio: window.devicePixelRatio,
                rootSize: {
                    width: rootWidth,
                    height: rootHeight,
                },
                viewPortMaxSize,
            });
        }
    };

    _isError = () => {
        let isError = false;
        const { sheetData } = this.props;
        if (sheetData) {
            const { length, data } = sheetData;
            if (length) {
                isError = length > config.MAX_CELL;
            } else if (data) {
                const col = data.length;
                const row = data[0].length;
                isError = col * row > config.MAX_CELL;
            }
        }
        return isError;
    };

    _onSaveExcel = () => {
        const { option } = this.props;
        const { saveExcel } = option?.callBack || {};
        if (saveExcel) {
            const saveData = getSaveData();
            saveExcel(saveData);
        }
    };

    render() {
        cLog('[SheetBox] render');
        const { option, sheetData } = this.props;
        const isError = this._isError();
        return (
            <div ref={this.sheetBoxRef} className={Styles.sheet_box}>
                <SheetBoxResizeSensor onResize={this._onResize} />
                <SheetBoxObserver changeMap={this._sheetBoxObserverMap} />
                <div className={Styles.empty_cell}>&nbsp;</div>
                <SheetTableContainer
                    option={option}
                    ref={this.sheetTableContainerRef}
                    updateViewport={this._updateViewPort}
                />
                <SheetOptionContainer option={option} completeEdit={this._completeEdit} />
                <ErrorDimmed isError={isError} onSaveExcel={this._onSaveExcel} />
                <CellSaveLayer isSave={sheetData?.isSave} option={option} />
            </div>
        );
    }
}

// export default SheetContainer;

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    selectCell: (selectCellItem) => dispatch(selectCell(selectCellItem)),
    selectEndCell: (selectCellItem) => dispatch(selectEndCell(selectCellItem)),
    setEdit: (editorData) => dispatch(setEdit(editorData)),
    addModifyRect: (sheetModifyRect) => dispatch(modifyRect(sheetModifyRect)),
    addEditorModifyData: (eModifyData) => dispatch(editorModifyData(eModifyData)),
    addViewPort: (viewPort) => dispatch(setViewPort(viewPort)),
    addRealViewPort: (viewPort) => dispatch(setRealViewPort(viewPort)),
    addClickCountPlus: () => dispatch(addClickCount()),

    pasteCell: (modifyCell) => dispatch(pasteCell(modifyCell)),
    deleteCell: (modifyCell) => dispatch(deleteCell(modifyCell)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(SheetBox);
