import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectModifyData = (state: StoreState) => state.editor.modifyData;

type ModifyDataHookProps = {
    changeModifyData?: (modifyData) => void;
};

const ModifyDataHook = (props: ModifyDataHookProps) => {
    const modifyData = useSelector(selectModifyData);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeModifyData } = props || {};
        if (changeModifyData) changeModifyData(modifyData);
    }, [modifyData]);

    return <></>;
};

export default ModifyDataHook;
