import { IBaseItem } from '@dataTypes';

export class SheetItem implements IBaseItem {
    str?: string | number;
    col?: number;
    row?: number;
    isDrag?: boolean;
    isSelect?: boolean;

    constructor(fieldItemData: { str?: string | number; col?: number; row?: number }) {
        this.str = fieldItemData.str;
        this.col = fieldItemData.col;
        this.row = fieldItemData.row;
    }

    concat() {
        const concatSheetItem = new SheetItem({ str: this.str, col: this.col, row: this.row });
        concatSheetItem.isDrag = this.isDrag;
        concatSheetItem.isSelect = this.isSelect;

        return concatSheetItem;
    }
}
