/* eslint-disable react/no-deprecated */
import React, { useEffect } from 'react';
import { Dispatch, connect } from 'react-redux';

import { ColumnEditData, RowEditData, SheetOption, StoreState } from '@dataTypes';
import CellOptionLayer from '@sheet/components/sheet-ui/cell-option/cell-option-layer';
import FieldOptionLayer from '@sheet/components/sheet-ui/field-option/field-option-layer';
import { ModifyData, MODIFY_TYPE } from '@sheet/model/modify-data';
import { ColumnEditType, RowEditType } from '..';
import { cLog } from '@debug/logger';

export type SheetOptionContainerProps = {
    option?: SheetOption;
    completeEdit?: () => void;
};

class SheetOptionContainer extends React.Component<SheetOptionContainerProps> {
    state = {};

    constructor(props) {
        super(props);
    }

    componentWillUnmount() {
        cLog('[SheetOptionContainer] componentWillUnmount');
    }

    componentWillMount() {
        cLog('[SheetOptionContainer] componentWillMount');
    }

    componentDidMount() {
        cLog('[SheetOptionContainer] componentDidMount');
    }

    componentDidUpdate() {
        cLog('[SheetOptionContainer] componentDidUpdate');
    }

    _onMaxCell = () => {
        const { maxCellPopup } = this.props.option?.callBack || {};
        if (maxCellPopup) maxCellPopup();
    };

    _onMinCell = () => {
        const { minCellPopup } = this.props.option?.callBack || {};
        if (minCellPopup) minCellPopup();
    };

    _onColumnEdit = (modifyData: ModifyData) => {
        const { onColumnEdit } = this.props.option?.callBack || {};
        if (onColumnEdit && modifyData.index !== undefined) {
            const columnEditData: ColumnEditData = {
                editType: modifyData.type === MODIFY_TYPE.ADD ? ColumnEditType.ADD : ColumnEditType.REMOVE,
                index: modifyData.index,
            };
            onColumnEdit(columnEditData);
        }
    };

    _onRowEdit = (modifyData: ModifyData) => {
        const { onRowEdit } = this.props.option?.callBack || {};
        if (onRowEdit && modifyData.index !== undefined) {
            const rowEditData: RowEditData = {
                editType: modifyData.type === MODIFY_TYPE.ADD ? RowEditType.ADD : RowEditType.REMOVE,
                index: modifyData.index,
            };
            onRowEdit(rowEditData);
        }
    };

    render() {
        const { completeEdit } = this.props;
        return (
            <>
                <FieldOptionLayer
                    onMaxCell={this._onMaxCell}
                    onMinCell={this._onMinCell}
                    onColumnEdit={this._onColumnEdit}
                    onRowEdit={this._onRowEdit}
                    completeEdit={completeEdit}
                />

                {/*      */}
                <CellOptionLayer onMaxCell={this._onMaxCell} />
            </>
        );
    }
}

// export default SheetContainer;

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // addSheetDatas: (sheetDatas) => dispatch(addSheetDatas(sheetDatas)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(SheetOptionContainer);
