import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectModifyData = (state: StoreState) => state.editor.modifyData;

type FieldOptionLayerObserverProps = {
    changeEditorModifyData?: (editorModifyData) => void;
};

const FieldOptionLayerObserver = (props: FieldOptionLayerObserverProps) => {
    const modifyData = useSelector(selectModifyData);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeEditorModifyData } = props;
        if (changeEditorModifyData) changeEditorModifyData(modifyData);
    }, [modifyData]);

    return <></>;
};

export default FieldOptionLayerObserver;
