import React, { useEffect, useState } from 'react';
import Styles from '@assets/scss/TableSheet.module.scss';
import { SheetOption } from '@dataTypes';
import config from '@sheet/config';

type CellSaveLayerProps = {
    isSave?: boolean;
    option?: SheetOption;
};

const CellSaveLayer = (props: CellSaveLayerProps) => {
    useEffect(() => {
        return () => {};
    }, []);

    let timeoutID;

    const onSaveToastrComplete = (e?) => {
        if (e) e.preventDefault();

        clearTimeout(timeoutID);
        setSave(false);
        const { saveToastrComplete } = props.option?.callBack || {};
        if (saveToastrComplete) saveToastrComplete();
    };

    const [isSave, setSave] = useState(false);
    useEffect(() => {
        if (isSave) {
            clearTimeout(timeoutID);
            timeoutID = setTimeout(() => {
                setSave(false);
                const { saveToastrComplete } = props.option?.callBack || {};
                if (saveToastrComplete) saveToastrComplete();
            }, 3000);
        }
    }, [isSave]);

    useEffect(() => {
        if (props.isSave) setSave(props.isSave);
    }, [props.isSave]);

    return isSave ? (
        <div className={Styles.save_layer} onClick={onSaveToastrComplete}>
            <strong>{config.LANGUAGE.saveCompleteTitle}</strong>
            <p>{config.LANGUAGE.saveCompleteContent}</p>
        </div>
    ) : (
        <></>
    );
};

export default CellSaveLayer;
