/* eslint-disable react/no-string-refs */
import React, { CSSProperties, useEffect, useState } from 'react';
import { useSelector, Dispatch, connect } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { SheetItem } from '@sheet/model';
import { MouseEventMap, SheetModifyData, SheetViewPort, StoreState } from '@dataTypes';
import { MODIFY_TYPE } from '@sheet/model/modify-data';
import { FIELD_TYPE } from './field-view';

import config from '@sheet/config';
import CellItem from '../common/cell-item';
import { EditData } from '@sheet/model/edit-data';
import { cLog } from '@debug/logger';
import { getColLength, getRowLength } from '@sheet/utils';

const { CELL_HEIGHT, CELL_WIDTH } = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const { CELL_RENDER_COUNT } = config.SHEET_TABLE;

const selectEditData = (state: StoreState) => state.editor.editData;

const nextRowItems = (props, items) => {
    const { MAX_RENDER } = config.SHEET_TABLE;
    const { rows } = props;
    const startIndex = items.length;

    if (startIndex === rows.length) return null;
    const nextCount = rows.length - startIndex;
    const len = nextCount > MAX_RENDER ? startIndex + MAX_RENDER : rows.length;
    const rowItems = rows.slice(startIndex, len);

    return rowItems;
};

type ColViewProps = {
    style: CSSProperties;
    rows: SheetItem[];
    colIndex: number;
    modifyData: SheetModifyData;
    mouseEventMap?: MouseEventMap;
    viewPort?: SheetViewPort;
};

const ColView = React.memo((props: ColViewProps) => {
    cLog('ColView ==> ');

    const addNewItems = () => {
        const { viewPort } = props;
        const newItems: any[] = [];
        let colViewStyle;
        if (viewPort) {
            const { sr, er } = viewPort?.rect || {};
            if (sr !== undefined && er !== undefined) {
                const { mouseEventMap } = props;
                const maxRowIndex = getRowLength() - 1;
                let startIndex = sr - CELL_RENDER_COUNT;
                let endIndex = er + CELL_RENDER_COUNT;
                startIndex = startIndex < 0 ? 0 : startIndex;
                endIndex = endIndex > maxRowIndex ? maxRowIndex : endIndex;

                colViewStyle = { transform: `translate(${CELL_WIDTH * startIndex}px, 0px)` };
                for (let i = startIndex; i <= endIndex; i++) {
                    const sheetItem = props.rows[i];
                    newItems.push(
                        <CellItem
                            style={colViewStyle}
                            ref={setCellItemRef(i)}
                            key={`${'CELL'}_${sheetItem.col}_${sheetItem.row}`}
                            data={sheetItem}
                            useCellOver={true}
                            mouseEventMap={mouseEventMap}
                        />,
                    );
                }
            }
        }

        addItems(newItems);
    };

    useEffect(() => {
        addNewItems();
    }, [props.viewPort]);

    const editData: EditData | undefined = useSelector(selectEditData);
    useEffect(() => {
        if (editData) {
            const { editItem } = editData;
            const { col, row } = editItem || {};
            if (col !== undefined && row !== undefined && col === props.colIndex) {
                if (refs[row]) refs[row].cellOverReset();
            }
        }
    }, [editData]);

    const setCellItemRef = (index) => (element) => {
        if (element) index < 0 ? refs.push(element) : refs.splice(index, 0, element);

        addRef(refs);
    };

    const prevRefRerender = (index) => {
        let prevIndex = (index || 0) - 1;
        const { rows } = props;
        if (prevIndex > -1) {
            for (let i = 0; i < 3; i++) {
                if (prevIndex < 0) return;
                const prevItem = rows[prevIndex];
                if (prevItem.str) {
                    if (refs[prevIndex]) refs[prevIndex].reRender();
                    return;
                }
                prevIndex--;
            }
        }
    };

    useEffect(() => {
        cLog('[ColView] Mount');
        return () => cLog('[ColView] Unmount');
    }, []);

    const [refs, addRef] = useState<any[]>([]);

    const [items, addItems] = useState<any[]>([]);

    useEffect(() => {
        const { modifyData, colIndex, mouseEventMap, viewPort } = props;

        if (modifyData) {
            cLog('modifyData props');
            const { type, index, fieldType, modifyItem, addRowCnt, rect } = modifyData;
            switch (type) {
                case MODIFY_TYPE.MODIFY:
                    if (modifyItem) {
                        const startCol = Number(modifyItem.col);

                        if (colIndex === startCol) {
                            const i = Number(modifyItem.row);
                            prevRefRerender(i);
                            if (refs[i]) refs[i].reRender();
                        }
                    }
                    break;

                case MODIFY_TYPE.ADD:
                    if (index !== undefined) {
                        const { rows } = props;
                        addNewItems();
                    }
                    break;

                case MODIFY_TYPE.REMOVE:
                    addNewItems();
                    break;

                case MODIFY_TYPE.COPY:
                case MODIFY_TYPE.CUT:
                    const { rows } = props;

                    if (modifyItem) {
                        const { sc, ec, sr, er } = rect || {};
                        const startCol = Number(modifyItem.col);
                        const startRow = Number(modifyItem.row);
                        const lastCol = startCol + ((ec || 0) - (sc || 0));
                        const lastRow = startRow + ((er || 0) - (sr || 0));

                        //  cell  copy/cut
                        if (startCol <= colIndex && lastCol >= colIndex) {
                            for (let ri = startRow; ri <= lastRow; ri++) {
                                if (refs[ri]) refs[ri].reRender();
                            }

                            prevRefRerender(startRow);
                        }

                        if (type === MODIFY_TYPE.CUT) {
                            const startCol = Number(sc || 0);
                            const lastCol = Number(ec || 0);

                            //  
                            if (startCol <= colIndex && lastCol >= colIndex) {
                                const startRow = Number(sr || 0);
                                const lastRow = Number(er || 0);
                                for (let ri = startRow; ri <= lastRow; ri++) {
                                    if (refs[ri]) refs[ri].reRender();
                                }

                                prevRefRerender(startRow);
                            }
                        }
                    }

                    // cell  
                    if (addRowCnt) {
                        const lastIndex = items.length;
                        prevRefRerender(lastIndex - 1);
                        addNewItems();
                    }
                    break;

                case MODIFY_TYPE.PASTE: {
                    const { start, end } = modifyData;
                    const startCol = Number(start?.col || 0);
                    const startRow = Number(start?.row || 0);
                    const lastCol = Number(end?.col || 0);
                    const lastRow = Number(end?.row || 0);
                    if (startCol <= colIndex && lastCol >= colIndex) {
                        prevRefRerender(startRow);
                        for (let ri = startRow; ri <= lastRow; ri++) {
                            if (refs[ri]) refs[ri].reRender();
                        }
                    }
                    break;
                }
                case MODIFY_TYPE.DELETE: {
                    const { sc, ec, sr, er } = rect || {};
                    const startCol = Number(sc || 0);
                    const startRow = Number(sr || 0);
                    const lastCol = Number(ec || 0);
                    const lastRow = Number(er || 0);
                    if (startCol <= colIndex && lastCol >= colIndex) {
                        prevRefRerender(startRow);
                        for (let ri = startRow; ri <= lastRow; ri++) {
                            if (refs[ri]) refs[ri].reRender();
                        }
                    }
                    break;
                }

                default:
                    break;
            }
        }
    }, [props.modifyData]);

    return (
        <div style={props.style} className={Styles.tr}>
            {items}
        </div>
    );
});

export type CellViewProps = {
    sheetItems: SheetItem[][];
    modifyData: SheetModifyData;
    mouseEventMap?: MouseEventMap;
    viewPort?: SheetViewPort;
    addStyle?: CSSProperties;
};

interface CellViewState {
    colItems: any[];
    colItemKeys: string[];
}

class CellView extends React.Component<CellViewProps, CellViewState> {
    state = {
        colItems: [],
        colItemKeys: [],
    };

    componentWillUnmount() {
        cLog('[CellView] componentWillUnmount');
    }

    // eslint-disable-next-line react/no-deprecated
    componentWillMount() {
        cLog('[CellView] componentWillMount');
    }

    componentDidMount() {
        cLog('[CellView] componentDidMount');
    }

    shouldComponentUpdate(_nextProps) {
        return true;
    }

    // eslint-disable-next-line react/no-deprecated
    componentWillReceiveProps(nextProps: CellViewProps) {
        cLog('[CellView] componentWillReceiveProps');
        const { sheetItems, modifyData, mouseEventMap, viewPort } = nextProps;
        const { type, fieldType, index, addColCnt } = modifyData || {};
        const { colItemKeys: itemKeys } = this.state;
        const colItemKeys: string[] = itemKeys || [];
        let style = {};

        if (viewPort) {
            let colItems: any[] = [];
            let colViewStyle: CSSProperties;
            const { sc, ec } = viewPort?.rect || {};
            if (sc !== undefined && ec !== undefined) {
                const maxColIndex = getColLength() - 1;
                let startIndex = sc - CELL_RENDER_COUNT;
                let endIndex = ec + CELL_RENDER_COUNT;

                startIndex = startIndex < 0 ? 0 : startIndex;
                endIndex = endIndex > maxColIndex ? maxColIndex : endIndex;

                const height = (endIndex - startIndex + 1) * CELL_HEIGHT;
                style = { transform: `translate(0px, ${CELL_HEIGHT * startIndex}px)`, height };

                colViewStyle = { transform: `translate(0px, ${CELL_HEIGHT * startIndex}px)` };
                let renderKey = 0;
                for (let i = startIndex; i <= endIndex; i++) {
                    if (!colItemKeys[i]) colItemKeys[i] = `${i}`;
                    const rows = sheetItems[i];
                    colItems.push(
                        <ColView
                            style={colViewStyle}
                            key={colItemKeys[i]}
                            colIndex={i}
                            rows={rows}
                            viewPort={viewPort}
                            modifyData={modifyData}
                            mouseEventMap={mouseEventMap}></ColView>,
                    );

                    renderKey++;
                }
            } else {
                colItems = (sheetItems || []).map((rows, index) => {
                    colViewStyle = { transform: 'translate(0px, 0px)' };
                    if (!colItemKeys[index]) colItemKeys[index] = `${index}`;

                    return (
                        <ColView
                            style={colViewStyle}
                            key={colItemKeys[index]}
                            colIndex={index}
                            rows={rows}
                            viewPort={viewPort}
                            modifyData={modifyData}
                            mouseEventMap={mouseEventMap}></ColView>
                    );
                });
            }

            this.setState({ colItems, colItemKeys });
        }
    }

    // eslint-disable-next-line react/no-deprecated
    componentWillUpdate() {
        cLog('[CellView] componentWillUpdate');
    }

    componentDidUpdate() {
        cLog('[CellView] componentDidUpdate');
    }

    render() {
        cLog('[CellView] render');
        const { colItems } = this.state;
        const { addStyle } = this.props;
        return <div style={addStyle}>{colItems}</div>;
    }
}

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (_dispatch: Dispatch) => ({});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(CellView);
