/* eslint-disable react/no-deprecated */
import React from 'react';
import Styles from '@assets/scss/TableSheet.module.scss';
import { FIELD_TYPE } from '../../sheet-table/field-view';
import { FieldOptionMouseEventMap } from '@dataTypes';
import { cLog } from '@debug/logger';
import config from '@sheet/config';

interface FieldOptionProps {
    fieldType?: string;
    fieldCount?: number;
    styleOption?: any;
    mouseEventMap?: FieldOptionMouseEventMap;
}

type TextMap = {
    blind?: string;
    addFirst?: string;
    addSecond?: string;
    remove?: string;
};

export class FieldOption extends React.Component<FieldOptionProps> {
    state = {
        openLine: false,
    };

    _textMap = {
        [FIELD_TYPE.TOP]: {
            addFirst: 'addLeftRow',
            addSecond: 'addRightRow',
            remove: 'deleteRow',
        },
        [FIELD_TYPE.TOP_GROUP]: {
            addFirst: 'addLeftRowGroup',
            addSecond: 'addRightRowGroup',
            remove: 'deleteRowGroup',
        },
        [FIELD_TYPE.LEFT]: {
            addFirst: 'addUpCol',
            addSecond: 'addDownCol',
            remove: 'deleteCol',
        },
        [FIELD_TYPE.LEFT_GROUP]: {
            addFirst: 'addUpColGroup',
            addSecond: 'addDownColGroup',
            remove: 'deleteColGroup',
        },
    };

    componentWillUnmount() {}

    componentWillMount() {}

    componentDidMount() {}

    componentDidUpdate() {
        cLog('[FieldOption] componentDidUpdate');
    }

    format(template, ...args) {
        return template.replace(/{(\d+)}/g, function (match, number) {
            return typeof args[number] !== 'undefined' ? args[number] : match;
        });
    }

    render() {
        const { fieldType, styleOption, mouseEventMap, fieldCount } = this.props;
        const textMap: TextMap = this._textMap[fieldType || ''] || {};
        const { addFirstClick, addSecondClick, removeClick } = mouseEventMap || {};
        const languageMap: any = config.LANGUAGE;

        // {/*    */}
        return (
            <div className={Styles.cell_layer} style={styleOption}>
                <ul>
                    <li>
                        <a href="/" className={Styles.link} onClick={addFirstClick}>
                            {this.format(languageMap[textMap.addFirst || ''], fieldCount)}
                        </a>
                    </li>
                    <li>
                        <a href="/" className={Styles.link} onClick={addSecondClick}>
                            {this.format(languageMap[textMap.addSecond || ''], fieldCount)}
                        </a>
                    </li>
                    <li>
                        <a href="/" className={Styles.link} onClick={removeClick}>
                            {this.format(languageMap[textMap.remove || ''], fieldCount)}
                        </a>
                    </li>
                </ul>
            </div>
        );
    }
}
