/* eslint-disable react/no-direct-mutation-state */
/* eslint-disable react/no-deprecated */
import React, { useEffect } from 'react';
import { useSelector, useDispatch, Dispatch, connect } from 'react-redux';
import { Fields, MouseEventMap, SheetModifyData, SheetOption, SheetViewPort, StoreState } from '@dataTypes';
import { FieldItem, SheetItem } from '@sheet/model';

import { FIELD_TYPE } from '@sheet/components/sheet-table/field-view';
import { editorActions } from '@sheet/reducers/editor-reducer';
import config from '@sheet/config';
import StoreManager from '@sheet/helper/store-manager';
import { ModifyData, MODIFY_TYPE } from '@sheet/model/modify-data';
import { EditorModifyData, EDITOR_MODIFY_TYPE } from '@sheet/model/editor-modify-data';
import FieldOptionLayerObserver from '@sheet/components/sheet-ui/field-option/field-option-layer-observer';
import { SheetOptionType } from '..';
import { cLog } from '@debug/logger';
import LeftCellGroup from '@sheet/components/sheet-table/left-cell-group';
import ContCellGroup from '@sheet/components/sheet-table/cont-cell-group';
import { getDevicePixelRatio } from '@sheet/utils';

const { selectCell, selectStartCell, selectEndCell, fieldSelectCell, fieldSelectEndCell, editorModifyData, setEdit } =
    editorActions;

const { CELL_OPTION_LAYER_SIZE, TABLE_SHEET_SIZE, FIELD_OPTION_LAYER_SIZE, SHEET_BOX_PADDING } = config.SHEET_TABLE;
const { BORDER_SIZE, FIRST_CELL_SIZE, CELL_WIDTH, CELL_HEIGHT, TOP_FIELD_HEIGHT, FIELD_OVER_MARGIN } = TABLE_SHEET_SIZE;

const { LEFT: SHEET_BOX_LEFT_PADDING, RIGHT: SHEET_BOX_RIGHT_PADDING } = SHEET_BOX_PADDING;

const {
    WIDTH: CELL_OPTION_WIDTH,
    HEIGHT_DEFAULT: CELL_OPTION_HEIGHT_DEFAULT,
    HEIGHT_ADD: CELL_OPTION_HEIGHT_ADD,
} = CELL_OPTION_LAYER_SIZE;

const { WIDTH: FIELD_OPTION_WIDTH, HEIGHT: FIELD_OPTION_HEIGHT } = FIELD_OPTION_LAYER_SIZE;

const FIELD_POSITION_DIRECTION = {
    LEFT: 0,
    RIGHT: 1,
    TOP: 0,
    BOTTOM: 1,
};

export type SheetTableContainerProps = {
    fields: Fields;
    modifyData: SheetModifyData;
    sheetItems: SheetItem[][];
    option?: SheetOption;
    viewPort?: SheetViewPort;

    selectCell: (selectCellItem) => void;
    selectEndCell: (selectCellItem) => void;
    selectStartCell: (selectCellItem) => void;
    fieldSelectCell: (modifyData) => void;
    fieldSelectEndCell: (modifyData) => void;
    addEditorModifyData: (eModifyData) => void;
    setEdit: (editorData) => void;

    updateViewport: (top?) => void;
};

const CELL_DOWN_DELAY = 500;

class SheetTableContainer extends React.Component<SheetTableContainerProps> {
    state = {
        cellMouseEventMap: {},
        fieldMouseEventMap: {},
        isFieldMouseDown: false,
        nowFieldType: null,
        isMouseDown: false,
    };

    _outSetTimeOutID?: number = undefined;

    contCellGroupRef: React.RefObject<typeof ContCellGroup>;
    leftCellGroupRef: React.RefObject<typeof LeftCellGroup>;

    _lastUpdateTopEvent: any;
    _synchronizationTimer: any = null;

    constructor(props) {
        super(props);

        let fieldMouseEventMap: MouseEventMap = {
            rightClick: (_data, e) => {
                e.preventDefault();
            },
        };
        let cellMouseEventMap: MouseEventMap = {
            rightClick: (_data, e) => {
                e.preventDefault();
            },
        };

        if (props.option) {
            const { type } = props.option;
            if (type === SheetOptionType.EDITOR) {
                fieldMouseEventMap = {
                    down: this._onFieldDown,
                    over: this._onFieldOver,
                    out: this._onFieldOut,
                    move: this._onFieldMove,
                    rightClick: this._onFieldRightClick,
                };

                cellMouseEventMap = {
                    dobuleClick: this._onCellDobuleClick,
                    down: this._onCellDown,
                    over: this._onCellOver,
                    rightClick: this._onCellRightClick,
                };
            }
        }

        this.state.cellMouseEventMap = cellMouseEventMap;
        this.state.fieldMouseEventMap = fieldMouseEventMap;

        this.contCellGroupRef = React.createRef();
        this.leftCellGroupRef = React.createRef();

        window.addEventListener('mouseup', this._onMouseUP);
    }

    componentWillUnmount() {
        cLog('[SheetTableContainer] componentWillUnmount');
        window.removeEventListener('mouseup', this._onMouseUP);
    }

    componentDidMount() {
        cLog('[SheetTableContainer] componentDidMount');
    }

    componentDidUpdate() {
        cLog('[SheetTableContainer] componentDidUpdate');
    }

    getRect = () => {
        return this.contCellGroupRef.current?.getRect();
    };

    updateScroll = (left, top) => {
        this.contCellGroupRef.current?.synchronizationScroll(left, top);
        this.leftCellGroupRef.current?.synchronizationScroll(left, top);
    };

    updateDevicePixelRatio = () => {
        this.contCellGroupRef.current?.updateDevicePixelRatio();
    };

    _onMouseUP = () => {
        this.state.isMouseDown = false;
        this.state.isFieldMouseDown = false;
    };

    _onFieldDown = (data, e: React.MouseEvent<HTMLDivElement>) => {
        const { nativeEvent, shiftKey } = e;
        const { button } = nativeEvent;

        this.state.isFieldMouseDown = true;
        this.state.nowFieldType = data.type;
        if (button === 0) {
            if (shiftKey) {
                this._selectFieldEndCell(data);
            } else {
                this._selectFieldCell(data);
            }
            this._selectModifyCell(undefined, e);
        } else if (button === 2) {
            this._selectModifyField(data, e);
        } else {
            cLog('');
        }
    };

    _onFieldOver = (data, _e) => {

        if (this.state.isFieldMouseDown) {
            this._selectFieldEndCell(data);
        }

        clearTimeout(this._outSetTimeOutID);
    };

    _onFieldOut = (_data, _e) => {
        // cLog('\n mouse out \n');
        const { editor } = StoreManager.state;
        const { modifyData } = editor;
        const { addEditorModifyData } = this.props;

        if (modifyData && !modifyData.openOption && modifyData.type !== EDITOR_MODIFY_TYPE.CELL) {
            this._outSetTimeOutID = setTimeout(() => {
                addEditorModifyData(undefined);
                cLog('remove');
            }, 100);
        }

        // cLog(data);
    };

    _createEditorModifyData = (data, position) => {
        const { type, col, row } = data;
        const { cols, rows } = this.props.fields || {};
        const { x, y, width, height, direction } = position;
        const positionCSS: any = { left: `${x}px`, top: `${y}px` };
        if (height) positionCSS.height = `${height}px`;
        if (width) positionCSS.width = `${width}px`;

        let modifyItem;

        if (type === FIELD_TYPE.TOP) {
            if (rows) modifyItem = rows[row + direction];
        } else if (type === FIELD_TYPE.LEFT) {
            if (cols) modifyItem = cols[col + direction];
        }

        if (!modifyItem) modifyItem = data;

        const editorModifyData: EditorModifyData = new EditorModifyData();
        editorModifyData.type = EDITOR_MODIFY_TYPE.FIELD;
        editorModifyData.modifyItem = modifyItem;
        editorModifyData.fieldType = type;
        editorModifyData.position = position;
        editorModifyData.positionCSS = positionCSS;
        editorModifyData.isLine = true;

        return editorModifyData;
    };

    _onFieldMove = (data, e) => {
        const { addEditorModifyData } = this.props;
        const { editor } = StoreManager.state;
        const { modifyData } = editor;

        let editorModifyData: EditorModifyData | undefined;

        if (modifyData) {
            if (modifyData.isLine && !modifyData.openOption) {
                const position = this._fieldOverPosition(data, e);

                if (position) {
                    if (
                        modifyData.position &&
                        modifyData.position.x === position.x &&
                        modifyData.position.y === position.y
                    ) {
                        //  
                    } else {
                        editorModifyData = this._createEditorModifyData(data, position);
                    }
                } else {
                    addEditorModifyData(undefined);
                }
            }
        } else {
            // ;
            const position = this._fieldOverPosition(data, e);

            if (position) {
                editorModifyData = this._createEditorModifyData(data, position);
                addEditorModifyData(editorModifyData);
            }
        }

        // cLog('mouse move');
        // cLog(offsetX, offsetY);
        // cLog(data);
    };

    _onFieldRightClick = (_data, e) => {
        e.preventDefault();
    };

    _onCellDobuleClick = (_data, _e, cursor?: number) => {
        cLog('CellDobuleClick');
        const { setEdit } = this.props;
        if (setEdit) setEdit({ cursor });
    };

    _onCellDown = (data, e: React.MouseEvent<HTMLDivElement>) => {
        const { nativeEvent, shiftKey } = e;
        const { button } = nativeEvent;
        if (button === 0) {
            if (shiftKey) {
                this._selectEndCell(data);
            } else {
                this._selectCell(data);
            }
            this._selectModifyCell(undefined, e);

            this.state.isMouseDown = true;
        } else if (button === 2) {
            const { editor } = StoreManager.state;
            const { rect } = editor;

            if (rect) {
                const { sc, ec, sr, er } = rect;
                const { col, row } = data;
                if (col >= sc && col <= ec && row >= sr && row <= er) {
                    this._selectModifyCell(data, e);
                } else {
                    this._selectCell(data);
                    this._selectModifyCell(data, e);
                }
            } else {
                this._selectCell(data);
                this._selectModifyCell(data, e);
            }
        } else {
            cLog('');
        }
    };

    _fieldOverPosition(data, e) {
        const { col, row, type } = data;
        const { nativeEvent } = e;
        const { offsetX, offsetY } = nativeEvent;
        let position: any = { x: 0, y: 0 };
        const { fields } = this.props;
        const { cols, rows } = fields || {};

        const rowLen = rows?.length || 1;
        const colLen = cols?.length || 1;

        const { editor } = StoreManager.state;
        const { realViewPort } = editor;
        let scrollLeft = 0;
        let scrollTop = 0;
        let viewPortWidth = CELL_WIDTH * rowLen;
        let viewPortHeight = CELL_HEIGHT * colLen;

        if (realViewPort) {
            scrollLeft = realViewPort.x;
            scrollTop = realViewPort.y;
            viewPortWidth = realViewPort.width;
            viewPortHeight = realViewPort.height;
        }

        if (type === FIELD_TYPE.TOP) {
            const rowIndex = row || 0;

            if (FIELD_OVER_MARGIN >= offsetX) {
                // 
                position.x = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + CELL_WIDTH * rowIndex - scrollLeft;
                position.height = TOP_FIELD_HEIGHT + viewPortHeight;
                position.direction = FIELD_POSITION_DIRECTION.LEFT;
            } else if (CELL_WIDTH - FIELD_OVER_MARGIN <= offsetX) {
                // 
                if (rowIndex === rowLen - 1) return null;
                position.x = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + CELL_WIDTH * (rowIndex + 1) - scrollLeft;
                position.height = TOP_FIELD_HEIGHT + viewPortHeight;
                position.direction = FIELD_POSITION_DIRECTION.RIGHT;
            } else {
                position = null;
            }
        } else {
            const colIndex = col || 0;

            position.x = SHEET_BOX_LEFT_PADDING;

            if (FIELD_OVER_MARGIN >= offsetY) {
                // 
                position.y = TOP_FIELD_HEIGHT + CELL_HEIGHT * colIndex - scrollTop;
                position.width = FIRST_CELL_SIZE + viewPortWidth;
                position.direction = FIELD_POSITION_DIRECTION.TOP;
            } else if (CELL_HEIGHT - FIELD_OVER_MARGIN <= offsetY) {
                // 
                if (colIndex === colLen - 1) return null;
                position.y = TOP_FIELD_HEIGHT + CELL_HEIGHT * (colIndex + 1) - scrollTop;
                position.width = FIRST_CELL_SIZE + viewPortWidth;
                position.direction = FIELD_POSITION_DIRECTION.BOTTOM;
            } else {
                position = null;
            }
        }

        return position;
    }

    _selectFieldCell = (data) => {
        // cLog(data);
        const { col, row, type } = data;
        const { sheet } = StoreManager.state;
        const { sheetItems } = sheet;
        let modifyData;
        if (sheetItems) {
            if (type === FIELD_TYPE.TOP) {
                const colLen = sheetItems.length - 1;
                if (colLen > -1) {
                    modifyData = new ModifyData();
                    modifyData.start = new FieldItem(sheetItems[0][row]);
                    modifyData.end = new FieldItem(sheetItems[colLen][row]);
                    modifyData.standardItem = data;
                }
            } else if (type === FIELD_TYPE.LEFT) {
                const rowLen = sheetItems[0].length - 1;
                if (rowLen > -1) {
                    modifyData = new ModifyData();
                    modifyData.start = new FieldItem(sheetItems[col][0]);
                    modifyData.end = new FieldItem(sheetItems[col][rowLen]);
                    modifyData.standardItem = data;
                }
            }
        }

        const { fieldSelectCell } = this.props;
        if (modifyData && fieldSelectCell) {
            fieldSelectCell(modifyData);
        }
    };

    _selectFieldEndCell = (data) => {
        const { col, row, type } = data;
        const { sheet } = StoreManager.state;
        const { sheetItems, fieldRect } = sheet;
        if ((col === 0 && fieldRect?.sc !== 0) || (row === 0 && fieldRect?.sr !== 0)) {
            return;
        }
        let modifyData;
        if (sheetItems) {
            if (type === FIELD_TYPE.TOP) {
                const colLen = sheetItems.length - 1;
                if (colLen > -1) {
                    modifyData = new ModifyData();
                    modifyData.start = new FieldItem(sheetItems[0][row]);
                    modifyData.end = new FieldItem(sheetItems[colLen][row]);
                    modifyData.standardItem = data;
                }
            } else if (type === FIELD_TYPE.LEFT) {
                const rowLen = sheetItems[0].length - 1;
                if (rowLen > -1) {
                    modifyData = new ModifyData();
                    modifyData.start = new FieldItem(sheetItems[col][0]);
                    modifyData.end = new FieldItem(sheetItems[col][rowLen]);
                    modifyData.standardItem = data;
                }
            }
        }

        const { fieldSelectEndCell } = this.props;
        if (modifyData && fieldSelectEndCell) {
            fieldSelectEndCell(modifyData);
        }
    };

    _selectModifyField = (data, e) => {
        const { addEditorModifyData } = this.props;

        let editorModifyData: EditorModifyData | undefined;
        if (data) {
            const { nativeEvent } = e;
            const { offsetX, offsetY } = nativeEvent;
            let left = 0;
            let top = 0;

            const { editor } = StoreManager.state;

            const { realViewPort, fieldRect } = editor;
            let scrollLeft = 0;
            let scrollTop = 0;
            let viewPortWidth = 0;
            let viewPortHeight = 0;

            if (realViewPort) {
                scrollLeft = realViewPort.x;
                scrollTop = realViewPort.y;
                viewPortWidth = realViewPort.viewPortMaxSize?.width || realViewPort.width;
                viewPortHeight = realViewPort.viewPortMaxSize?.height || realViewPort.height;
            }

            const { col, row, type } = data;
            let fieldType = type;
            let fieldCount = 1;
            if (type === FIELD_TYPE.TOP) {
                left = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + CELL_WIDTH * (row || 0) + offsetX - scrollLeft;
                top = offsetY;
                if (fieldRect && fieldRect.sr !== fieldRect.er && fieldRect.sr <= row && fieldRect.er >= row) {
                    fieldType = FIELD_TYPE.TOP_GROUP;
                    fieldCount = fieldRect.er - fieldRect.sr + 1;
                }
            } else {
                left = SHEET_BOX_LEFT_PADDING + offsetX;
                top = TOP_FIELD_HEIGHT + CELL_HEIGHT * (col || 0) + offsetY;

                if (fieldRect && fieldRect.sc !== fieldRect.ec && fieldRect.sc <= col && fieldRect.ec >= col) {
                    fieldType = FIELD_TYPE.LEFT_GROUP;
                    fieldCount = fieldRect.ec - fieldRect.sc + 1;
                }
            }

            const maxX = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + viewPortWidth;
            const maxY = TOP_FIELD_HEIGHT + viewPortHeight;
            const optionMaxX = left + FIELD_OPTION_WIDTH;
            const optionMaxY = top + FIELD_OPTION_HEIGHT;

            if (maxX < optionMaxX) left += maxX - optionMaxX;
            if (maxY < optionMaxY) top += maxY - optionMaxY;

            const position = { x: left, y: top };
            const positionCSS = { left: `${left}px`, top: `${top}px` };

            editorModifyData = new EditorModifyData();
            editorModifyData.type = EDITOR_MODIFY_TYPE.FIELD;
            editorModifyData.fieldType = fieldType;
            editorModifyData.fieldCount = fieldCount;
            editorModifyData.modifyItem = data;
            editorModifyData.position = position;
            editorModifyData.positionCSS = positionCSS;
            editorModifyData.openOption = true;
        }

        addEditorModifyData(editorModifyData);
    };

    _selectModifyCell = (data, e) => {
        const { addEditorModifyData } = this.props;

        let editorModifyData: EditorModifyData | undefined;
        if (data) {
            // const { nativeEvent } = e;
            // const { offsetX, offsetY } = nativeEvent;

            // const { col, row } = data;
            // let left = CELL_WIDTH * (row || 0) + offsetX;
            // let top = CELL_HEIGHT * (col || 0) + offsetY;

            // const { editor } = StoreManager.state;
            // const { modifyRect, realViewPort } = editor;

            // if (realViewPort) {
            //     const { x, y, width, height } = realViewPort;
            //     const maxX = x + width;
            //     const maxY = y + height;
            //     const optionMaxX = left + CELL_OPTION_WIDTH;
            //     const optionMaxY = top + CELL_OPTION_HEIGHT_DEFAULT + (modifyRect ? CELL_OPTION_HEIGHT_ADD : 0);

            //     if (maxX < optionMaxX) left += maxX - optionMaxX;
            //     if (maxY < optionMaxY) top += maxY - optionMaxY;
            // }

            // const position = { x: left, y: top };
            // const positionCSS = { left: `${left}px`, top: `${top}px`, width: '120px' };
            const { nativeEvent } = e;
            const { offsetX, offsetY } = nativeEvent;
            let left = 0;
            let top = 0;

            const { editor } = StoreManager.state;
            const { modifyRect, realViewPort } = editor;

            let scrollLeft = 0;
            let scrollTop = 0;
            let viewPortWidth = 0;
            let viewPortHeight = 0;

            if (realViewPort) {
                scrollLeft = realViewPort.x;
                scrollTop = realViewPort.y;
                viewPortWidth = realViewPort.viewPortMaxSize?.width || realViewPort.width;
                viewPortHeight = realViewPort.viewPortMaxSize?.height || realViewPort.height;
            }

            const { col, row } = data;
            left = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + CELL_WIDTH * (row || 0) + offsetX - scrollLeft;
            top = TOP_FIELD_HEIGHT + CELL_HEIGHT * (col || 0) + offsetY - scrollTop;

            const maxX = SHEET_BOX_LEFT_PADDING + FIRST_CELL_SIZE + viewPortWidth;
            const maxY = TOP_FIELD_HEIGHT + viewPortHeight;

            const optionMaxX = left + CELL_OPTION_WIDTH - (SHEET_BOX_LEFT_PADDING >> 1);
            const optionMaxY = top + CELL_OPTION_HEIGHT_DEFAULT + (modifyRect ? CELL_OPTION_HEIGHT_ADD : 0);

            if (maxX < optionMaxX) left += maxX - optionMaxX;
            if (maxY < optionMaxY) top += maxY - optionMaxY;

            const position = { x: left, y: top };
            const positionCSS = { left: `${left}px`, top: `${top}px` };

            editorModifyData = new EditorModifyData();
            editorModifyData.type = EDITOR_MODIFY_TYPE.CELL;
            editorModifyData.modifyItem = data;
            editorModifyData.position = position;
            editorModifyData.positionCSS = positionCSS;
        }

        addEditorModifyData(editorModifyData);
    };

    _selectCell = (data) => {
        const { selectCell } = this.props;
        if (selectCell) selectCell({ selectCell: data });
    };

    _selectEndCell = (data) => {
        const { selectEndCell } = this.props;
        if (selectEndCell) selectEndCell(data);
    };

    _onCellOver = (data) => {
        if (this.state.isMouseDown) {
            const { selectEndCell } = this.props;
            if (selectEndCell) selectEndCell(data);
        } else if (this.state.isFieldMouseDown) {
            data.type = this.state.nowFieldType;
            this._selectFieldEndCell(data);
        }
    };

    _onCellRightClick = (_data, e) => {
        e.preventDefault();
    };

    _onChangeEditorModifyData = (editorModifyData) => {
        if (editorModifyData) {
            const { openOption, modifyItem, over, fieldType } = editorModifyData;

            if (openOption && (fieldType === FIELD_TYPE.TOP || fieldType === FIELD_TYPE.LEFT)) {
                this._selectFieldCell(modifyItem);
            }
            if (over) clearTimeout(this._outSetTimeOutID);
        }
    };

    _updateTop = (e) => {
        this._lastUpdateTopEvent = e;
        this._synchronizationTop();

        const { updateViewport } = this.props;
        if (updateViewport) updateViewport();

        if (this._synchronizationTimer) clearTimeout(this._synchronizationTimer);
        this._synchronizationTimer = setTimeout(this._synchronizationTop, 200);
    };

    _synchronizationTop = () => {
        if (this._lastUpdateTopEvent) {
            const target = this._lastUpdateTopEvent.target;
            const top = target.scrollTop;
            this.contCellGroupRef.current?.synchronizationTop(top, target);
            this.leftCellGroupRef.current?.synchronizationTop(top, target);
        }
    };

    _updateLeft = (_e) => {
        const { updateViewport } = this.props;
        if (updateViewport) updateViewport();
    };

    _updateCheck = (width, height) => {
        const { updateViewport, modifyData, viewPort } = this.props;
        const { addColCnt, addRowCnt, type, fieldType } = modifyData || {};
        if (addColCnt || addRowCnt) updateViewport();

        if (type === MODIFY_TYPE.ADD) {
            const { rootSize } = viewPort || {};
            const { width: viewPortWidth, height: viewPortHeight } = rootSize || {};
            if (fieldType === FIELD_TYPE.TOP && width < (viewPortWidth || 0)) updateViewport();
            if (fieldType === FIELD_TYPE.LEFT && height < (viewPortHeight || 0)) updateViewport();
        }
    };

    render() {
        const { cellMouseEventMap, fieldMouseEventMap } = this.state;
        const { sheetItems, fields, modifyData, viewPort, option } = this.props;
        const { cols, rows } = fields || {};
        const height = CELL_HEIGHT * (cols?.length || 1);
        const width = BORDER_SIZE + CELL_WIDTH * (rows?.length || 1) + getDevicePixelRatio();

        this._updateCheck(width, height);

        /*   */
        return (
            <>
                <FieldOptionLayerObserver changeEditorModifyData={this._onChangeEditorModifyData} />
                <LeftCellGroup
                    ref={this.leftCellGroupRef}
                    cellHeight={height}
                    fields={fields}
                    viewPort={viewPort}
                    modifyData={modifyData}
                    fieldMouseEventMap={fieldMouseEventMap}
                    updateTop={this._updateTop}
                />
                <ContCellGroup
                    ref={this.contCellGroupRef}
                    option={option}
                    cellHeight={height}
                    fields={fields}
                    viewPort={viewPort}
                    sheetItems={sheetItems}
                    modifyData={modifyData}
                    fieldMouseEventMap={fieldMouseEventMap}
                    cellMouseEventMap={cellMouseEventMap}
                    updateTop={this._updateTop}
                    updateLeft={this._updateLeft}
                />
            </>
        );
    }
}

const mapStateToProps = (state: StoreState) => {
    return {
        sheetItems: state.sheet.sheetItems,
        fields: state.sheet.fields,
        modifyData: state.sheet.modifyData,
        viewPort: state.editor.viewPort,
    };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
    selectCell: (selectCellItem) => dispatch(selectCell(selectCellItem)),
    selectStartCell: (selectCellItem) => dispatch(selectStartCell(selectCellItem)),
    selectEndCell: (selectCellItem) => dispatch(selectEndCell(selectCellItem)),
    fieldSelectCell: (modifyData) => dispatch(fieldSelectCell(modifyData)),
    fieldSelectEndCell: (modifyData) => dispatch(fieldSelectEndCell(modifyData)),
    addEditorModifyData: (eModifyData) => dispatch(editorModifyData(eModifyData)),
    setEdit: (editorData) => dispatch(setEdit(editorData)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(SheetTableContainer);
