/* eslint-disable react/jsx-no-undef */
/* eslint-disable react/no-deprecated */
import React, { useEffect } from 'react';
import { Dispatch, connect } from 'react-redux';
import { IBaseItem, SheetData, SheetOption, StoreState } from '@dataTypes';
import { sheetActions } from '@sheet/reducers/sheet-reducer';
import SheetBox from '@sheet/components/sheet/sheet-box';

import { ModifyData } from '@sheet/model/modify-data';
import { cLog } from '@debug/logger';

const { addSheetDatas, modifySheetData, modifyRowCell, modifyColCell, copyCell, deleteCell, cutCell } = sheetActions;

export type SheetContainerProps = {
    sheetData?: SheetData;
    option?: SheetOption;

    addSheetDatas: (sheetDatas) => void;
    modifySheetData: (sheetData) => void;
    modifyRowCell: (modifyCell) => void;
    modifyColCell: (modifyCell) => void;
    copyCell: (modifyCell) => void;
    deleteCell: (modifyCell) => void;
    cutCell: (modifyCell) => void;
};

class SheetContainer extends React.Component<SheetContainerProps> {
    state = {};

    sheetBoxRef: React.RefObject<HTMLDivElement>;

    constructor(props) {
        super(props);

        this.sheetBoxRef = React.createRef();
    }

    componentWillUnmount() {
        cLog('[SheetContainer] componentWillUnmount');
    }

    componentWillMount() {
        cLog('[SheetContainer] componentWillMount');
        const { addSheetDatas, sheetData, option } = this.props;
        if (sheetData) addSheetDatas({ sheetData, option });
    }

    componentDidMount() {
        cLog('[SheetContainer] componentDidMount');
    }

    componentWillReceiveProps(nextProps: SheetContainerProps) {
        cLog('componentWillReceiveProps');
        cLog(nextProps);
    }

    componentDidUpdate() {
        cLog('[SheetContainer] componentDidUpdate');
        const { addSheetDatas, sheetData, option } = this.props;
        if (sheetData) addSheetDatas({ sheetData, option });
    }

    // testCode
    testChangeSheetData(sheetData: IBaseItem) {
        const { modifySheetData } = this.props;
        if (sheetData) modifySheetData(sheetData);
    }

    testModifyRowCell(modifyData: ModifyData) {
        const { modifyRowCell } = this.props;
        if (modifyData) modifyRowCell(modifyData);
    }

    testModifyColCell(modifyData: ModifyData) {
        const { modifyColCell } = this.props;
        if (modifyData) modifyColCell(modifyData);
    }

    testCopyCell(modifyData: ModifyData) {
        const { copyCell } = this.props;
        if (modifyData) copyCell(modifyData);
    }

    testDeleteCell(modifyData: ModifyData) {
        const { deleteCell } = this.props;
        if (modifyData) deleteCell(modifyData);
    }

    testCutCell(modifyData: ModifyData) {
        const { cutCell } = this.props;
        if (modifyData) cutCell(modifyData);
    }

    render() {
        const { option, sheetData } = this.props;
        return <SheetBox sheetData={sheetData} option={option} />;
    }
}

// export default SheetContainer;

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    addSheetDatas: (sheetDatas) => dispatch(addSheetDatas(sheetDatas)),
    modifySheetData: (sheetDatas) => dispatch(modifySheetData(sheetDatas)),
    modifyRowCell: (modifyCell) => dispatch(modifyRowCell(modifyCell)),
    modifyColCell: (modifyCell) => dispatch(modifyColCell(modifyCell)),
    copyCell: (modifyCell) => dispatch(copyCell(modifyCell)),
    deleteCell: (modifyCell) => dispatch(deleteCell(modifyCell)),
    cutCell: (modifyCell) => dispatch(cutCell(modifyCell)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(SheetContainer);
