/* eslint-disable react/no-deprecated */
import React from 'react';
import { Dispatch, connect } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { IBaseItem, StoreState } from '@dataTypes';
import { EditorModifyData, EDITOR_MODIFY_TYPE } from '@sheet/model/editor-modify-data';
import { FIELD_TYPE } from '../../sheet-table/field-view';
import { FieldOption } from './field-option';
import { editorActions } from '@sheet/reducers/editor-reducer';
import { sheetActions } from '@sheet/reducers/sheet-reducer';
import { ModifyData, MODIFY_TYPE } from '@sheet/model/modify-data';
import StoreManager from '@sheet/helper/store-manager';
import config from '@sheet/config';
import { cLog } from '@debug/logger';
import { CSSProperties } from 'styled-components';

const { editorModifyData, fieldSelectCell, modifyRect } = editorActions;
const { modifyRowCell, modifyColCell } = sheetActions;

const { TABLE_SHEET_SIZE, FIELD_OPTION_LAYER_SIZE } = config.SHEET_TABLE;
const { WIDTH: FIELD_OPTION_WIDTH, HEIGHT: FIELD_OPTION_HEIGHT } = FIELD_OPTION_LAYER_SIZE;
const { FIRST_CELL_SIZE, TOP_FIELD_HEIGHT, CELL_WIDTH, CELL_HEIGHT } = TABLE_SHEET_SIZE;

interface FieldItemProps {
    modifyData?: EditorModifyData;
    onMaxCell?: () => void;
    onMinCell?: () => void;
    onColumnEdit?: (modifyData) => void;
    onRowEdit?: (modifyData) => void;
    completeEdit?: () => void;

    addEditorModifyData: (eModifyData) => void;
    modifyRowCell: (modifyData) => void;
    modifyColCell: (modifyData) => void;

    fieldSelectCell: (modifyData) => void;
    modifyRect: (modifyData) => void;
}

type TextMap = {
    blind?: string;
    addFirst?: string;
    addSecond?: string;
    remove?: string;
};

enum ADD_ROW_TYPE {
    PREV = 'prev',
    NEXT = 'next',
}

class FieldOptionLayer extends React.Component<FieldItemProps> {
    state = {};

    _textMap = {
        [FIELD_TYPE.TOP]: {
            blind: ' ',
        },
        [FIELD_TYPE.TOP_GROUP]: {
            blind: ' ',
        },
        [FIELD_TYPE.LEFT]: {
            blind: ' ',
        },
        [FIELD_TYPE.LEFT_GROUP]: {
            blind: ' ',
        },
    };

    _layerPosition = {
        [FIELD_TYPE.TOP]: {
            width: '164px',
        },
        [FIELD_TYPE.TOP_GROUP]: {
            width: '164px',
        },
        [FIELD_TYPE.LEFT]: {
            width: '164px',
        },
        [FIELD_TYPE.LEFT_GROUP]: {
            width: '164px',
        },
    };

    _layerOpenPosition = {
        [FIELD_TYPE.TOP]: {
            left: 8,
            top: 24,
            width: '164px',
        },
        [FIELD_TYPE.TOP_GROUP]: {
            left: 8,
            top: 24,
            width: '164px',
        },
        [FIELD_TYPE.LEFT]: {
            left: 35,
            top: 8,
            width: '164px',
        },
        [FIELD_TYPE.LEFT_GROUP]: {
            left: 35,
            top: 8,
            width: '164px',
        },
    };

    _mouseMap = {};

    constructor(props) {
        super(props);

        this._mouseMap = {
            addFirstClick: this._onAddFirstClick,
            addSecondClick: this._onAddSecondClick,
            removeClick: this._onRemoveClick,
        };
    }

    componentWillUnmount() {}

    componentWillMount() {}

    componentDidMount() {}

    componentDidUpdate() {
        cLog('[CellOptionLayer] componentDidUpdate');
    }

    isTopField(fieldType) {
        return fieldType === FIELD_TYPE.TOP || fieldType === FIELD_TYPE.TOP_GROUP;
    }

    isLeftField(fieldType) {
        return fieldType === FIELD_TYPE.LEFT || fieldType === FIELD_TYPE.LEFT_GROUP;
    }

    _isAddField = (fieldType) => {
        const { fields } = StoreManager.state.sheet;
        const { cols, rows } = fields;

        let rowLen = rows?.length || 0;
        let colLen = cols?.length || 0;

        if (this.isTopField(fieldType)) {
            rowLen += 1;
        } else {
            colLen += 1;
        }

        if (rowLen * colLen > config.MAX_CELL) {
            const { onMaxCell } = this.props;
            if (onMaxCell) onMaxCell();
            return false;
        }

        return true;
    };

    _isRemoveField = (fieldType) => {
        const { fields } = StoreManager.state.sheet;
        const { cols, rows } = fields;
        const checkItem = (this.isTopField(fieldType) ? rows : cols) || [];

        if (checkItem.length <= 1) {
            const { onMinCell } = this.props;
            if (onMinCell) onMinCell();
            return false;
        }

        return true;
    };

    _modifyField = (type: ADD_ROW_TYPE, modifyType: MODIFY_TYPE, isFieldOption?: boolean) => {
        const {
            onColumnEdit,
            onRowEdit,
            modifyData,
            modifyRowCell,
            modifyColCell,
            addEditorModifyData,
            fieldSelectCell,
            modifyRect,
        } = this.props;
        if (modifyData) {
            const { modifyItem: fieldItem, fieldType, fieldCount = 1 } = modifyData;
            if (fieldItem !== undefined) {
                const { row, col } = fieldItem;
                const mData = new ModifyData();
                mData.type = modifyType;
                mData.fieldType = fieldItem.type;
                mData.fieldCount = fieldCount;

                const { fields } = StoreManager.state.sheet;
                const { cols, rows } = fields;
                const rowLen = rows?.length || 0;
                const colLen = cols?.length || 0;
                const rowLastIndex = rowLen - 1;
                const colLastIndex = colLen - 1;

                if (modifyType === MODIFY_TYPE.REMOVE && !this._isRemoveField(fieldType)) {
                    addEditorModifyData(undefined);
                    return;
                }

                if (modifyType === MODIFY_TYPE.REMOVE) {
                    if (this.isTopField(fieldType)) {
                        if (row === rowLastIndex) fieldSelectCell(undefined);
                    } else {
                        if (col === colLastIndex) fieldSelectCell(undefined);
                    }

                    modifyRect(undefined);
                }

                addEditorModifyData(undefined);

                if (modifyType === MODIFY_TYPE.ADD && !this._isAddField(fieldType)) {
                    return;
                }

                if (this.isTopField(fieldType)) {
                    mData.index = (row || 0) + (type === ADD_ROW_TYPE.PREV ? 0 : 1);
                    mData.directionType = type;
                    mData.isFieldOption = isFieldOption;
                    modifyRowCell(mData);
                    if (onColumnEdit) onColumnEdit(mData);
                } else {
                    mData.index = (col || 0) + (type === ADD_ROW_TYPE.PREV ? 0 : 1);
                    mData.directionType = type;
                    mData.isFieldOption = isFieldOption;
                    modifyColCell(mData);
                    if (onRowEdit) onRowEdit(mData);
                }
            }
        }
    };

    _onAddFirstClick = (e) => {
        e.preventDefault();
        this._modifyField(ADD_ROW_TYPE.PREV, MODIFY_TYPE.ADD);
    };

    _onAddSecondClick = (e) => {
        e.preventDefault();
        this._modifyField(ADD_ROW_TYPE.NEXT, MODIFY_TYPE.ADD);
    };

    _onRemoveClick = (e) => {
        e.preventDefault();
        this._modifyField(ADD_ROW_TYPE.PREV, MODIFY_TYPE.REMOVE);
        cLog('_onRemoveClick');
    };

    _onContextMenu = (e) => {
        e.preventDefault();
    };

    _onFieldOptionOpen = (e) => {
        e.preventDefault();

        const { editor } = StoreManager.state;
        if (editor.editData) {
            const { completeEdit } = this.props;
            if (completeEdit) completeEdit();
        }

        this._modifyField(ADD_ROW_TYPE.PREV, MODIFY_TYPE.ADD, true);
    };

    _onAddFieldOptionOver = (_e) => {
        cLog('_onAddFieldOptionOver');
        const { addEditorModifyData, modifyData } = this.props;
        if (modifyData) {
            const editorModifyData = modifyData.clone();
            editorModifyData.over = true;
            if (addEditorModifyData) addEditorModifyData(editorModifyData);
        }
    };

    _onAddFieldOptionOut = (_e) => {
        cLog('_onAddFieldOptionOut');
    };

    render() {
        const { modifyData } = this.props;
        const optionPosition: CSSProperties = {
            zIndex: 120,
        };
        const linePosition: any = {};
        let isShow;
        let fieldType;
        let textMap: TextMap = {};
        let useLine;
        let isOptionOpen;
        let layerOpenPosition: any = {};
        let fieldCount;

        if (modifyData) {
            const { positionCSS, type, isLine, openOption, position } = modifyData;
            fieldType = modifyData.fieldType;
            fieldCount = modifyData.fieldCount;
            const { left, top } = positionCSS || {};
            const { width: pWidth, height: pHeight } = position || {};
            if (left) optionPosition.left = left;
            if (top) optionPosition.top = top;

            const { sheet } = StoreManager.state;
            const { fields } = sheet;
            const { cols, rows } = fields;

            if (pWidth) {
                let lineWidth = (rows || []).length * CELL_WIDTH + FIRST_CELL_SIZE || 0;
                if (lineWidth > pWidth) lineWidth = pWidth;
                linePosition.width = lineWidth;
            }

            if (pHeight) {
                let lineHeight = (cols || []).length * CELL_HEIGHT + TOP_FIELD_HEIGHT || 0;
                if (lineHeight > pHeight) lineHeight = pHeight;
                linePosition.height = lineHeight;
            }

            isShow = type === EDITOR_MODIFY_TYPE.FIELD;
            textMap = this._textMap[fieldType || ''] || {};
            useLine = isLine;
            isOptionOpen = openOption;

            if (openOption) {
                const { editor } = StoreManager.state;
                const { realViewPort } = editor;

                if (realViewPort && position) {
                    const openPosition = this._layerOpenPosition[fieldType];
                    let left = openPosition.left;
                    let top = openPosition.top;
                    const { x, y, width, height } = realViewPort;
                    const maxX = x + width;
                    const maxY = y + height;
                    const optionMaxX = left + position.x + FIELD_OPTION_WIDTH;
                    const optionMaxY = top + position.y + FIELD_OPTION_HEIGHT;

                    if (maxX < optionMaxX) left += maxX - optionMaxX;
                    if (maxY < optionMaxY) top += maxY - optionMaxY;

                    layerOpenPosition = {
                        ...openPosition,
                        left: `${left}px`,
                        top: `${top}px`,
                    };
                }
            }
        }

        // {/*    */}
        return modifyData && isShow ? (
            <div
                className={fieldType === FIELD_TYPE.TOP ? Styles.col_add : Styles.row_add}
                style={optionPosition}
                onContextMenu={this._onContextMenu}>
                {useLine ? (
                    <>
                        <a
                            href="/"
                            role="button"
                            onClick={this._onFieldOptionOpen}
                            onMouseOver={this._onAddFieldOptionOver}
                            onMouseOut={this._onAddFieldOptionOut}>
                            <span className={Styles.blind}>{textMap.blind}</span>
                        </a>
                        {isOptionOpen ? (
                            <FieldOption
                                fieldType={fieldType}
                                fieldCount={fieldCount}
                                styleOption={layerOpenPosition}
                                mouseEventMap={this._mouseMap}
                            />
                        ) : (
                            <></>
                        )}
                        <span className={Styles.line} style={linePosition}>
                            &nbsp;
                        </span>
                    </>
                ) : (
                    <FieldOption
                        fieldType={fieldType}
                        fieldCount={fieldCount}
                        styleOption={this._layerPosition[fieldType]}
                        mouseEventMap={this._mouseMap}
                    />
                )}
            </div>
        ) : (
            <></>
        );
    }
}

const mapStateToProps = (state: StoreState) => {
    return {
        modifyData: state.editor.modifyData,
    };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
    addEditorModifyData: (eModifyData) => dispatch(editorModifyData(eModifyData)),

    modifyRowCell: (modifyData) => dispatch(modifyRowCell(modifyData)),
    modifyColCell: (modifyData) => dispatch(modifyColCell(modifyData)),

    fieldSelectCell: (modifyData) => dispatch(fieldSelectCell(modifyData)),
    modifyRect: (modifyData) => dispatch(modifyRect(modifyData)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(FieldOptionLayer);
