import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectEditData = (state: StoreState) => state.editor.editData;
const selectSelectCell = (state: StoreState) => state.editor.selectCell;
const selectModifyRect = (state: StoreState) => state.editor.modifyRect;
const selectModifyData = (state: StoreState) => state.editor.modifyData;
const selectViewPort = (state: StoreState) => state.editor.viewPort;
export interface SheetBoxObserverMap {
    changeSelectCell?: (selectCell) => void;
    changeEditData?: (editData) => void;
    changeModifyRect?: (modifyRect) => void;
    changeModifyData?: (modifyData) => void;
    changeViewPort?: (viewPort) => void;
}

type SheetBoxObserverProps = {
    changeMap?: SheetBoxObserverMap;
};

const SheetBoxObserver = (props: SheetBoxObserverProps) => {
    const editData = useSelector(selectEditData);
    const selectCell = useSelector(selectSelectCell);
    const modifyRect = useSelector(selectModifyRect);
    const modifyData = useSelector(selectModifyData);
    const viewPort = useSelector(selectViewPort);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeEditData } = props?.changeMap || {};
        if (changeEditData) changeEditData(editData);
    }, [editData]);

    useEffect(() => {
        const { changeSelectCell } = props?.changeMap || {};
        if (changeSelectCell) changeSelectCell(selectCell);
    }, [selectCell]);

    useEffect(() => {
        const { changeModifyRect } = props?.changeMap || {};
        if (changeModifyRect) changeModifyRect(modifyRect);
    }, [modifyRect]);

    useEffect(() => {
        const { changeModifyData } = props?.changeMap || {};
        if (changeModifyData) changeModifyData(modifyData);
    }, [modifyData]);

    useEffect(() => {
        const { changeViewPort } = props?.changeMap || {};
        if (changeViewPort) changeViewPort(viewPort);
    }, [viewPort]);

    return <></>;
};

export default SheetBoxObserver;
