/* eslint-disable react/no-deprecated */
import React, { CSSProperties, useEffect } from 'react';
import { Dispatch, connect } from 'react-redux';
import { StoreState } from '@dataTypes';

export type SheetBoxResizeSensorProps = {
    onResize?: () => void;
};

class SheetBoxResizeSensor extends React.Component<SheetBoxResizeSensorProps> {
    state = {
        expandChildHeight: 0,
        expandChildWidth: 0,
        expandScrollLeft: 0,
        expandScrollTop: 0,
        shrinkScrollTop: 0,
        shrinkScrollLeft: 0,
        lastWidth: 0,
        lastHeight: 0,
    };

    containerRef: React.RefObject<HTMLDivElement>;
    expandRef: React.RefObject<HTMLDivElement>;
    shrinkRef: React.RefObject<HTMLDivElement>;

    constructor(props) {
        super(props);

        this.expandRef = React.createRef();
        this.shrinkRef = React.createRef();
        this.containerRef = React.createRef();
    }

    componentDidMount() {
        this.reset();
    }

    reset = () => {
        const container = this.containerRef.current;
        const expand = this.expandRef.current;
        const shrink = this.shrinkRef.current;

        if (container && expand && shrink) {
            this.setState({
                expandChildHeight: expand.offsetHeight + 10,
                expandChildWidth: expand.offsetWidth + 10,
                lastWidth: container.parentElement?.offsetWidth || container.offsetWidth,
                lastHeight: container.parentElement?.offsetHeight || container.offsetHeight,
            });

            expand.scrollLeft = expand.scrollWidth;
            expand.scrollTop = expand.scrollHeight;

            shrink.scrollLeft = shrink.scrollWidth;
            shrink.scrollTop = shrink.scrollHeight;
        }
    };

    handleScroll = (_evt) => {
        const { state } = this;

        const container = this.containerRef.current;

        if (container) {
            const containerWidth = container.parentElement?.offsetWidth || container.offsetWidth;
            const containerHeight = container.parentElement?.offsetHeight || container.offsetHeight;
            if (containerWidth !== state.lastWidth || containerHeight !== state.lastHeight) {
                const { onResize } = this.props;
                if (onResize) onResize();
            }

            this.reset();
        }
    };

    render() {
        const { state } = this;

        const parentStyle: CSSProperties = {
            position: 'absolute',
            left: 0,
            top: 0,
            right: 0,
            bottom: 0,
            overflow: 'scroll',
            zIndex: -1,
            visibility: 'hidden',
        };

        const childStyle: CSSProperties = {
            position: 'absolute',
            left: 0,
            top: 0,
        };

        const expandChildStyle: CSSProperties = {
            ...childStyle,
            width: state.expandChildWidth,
            height: state.expandChildHeight,
        };

        const shrinkChildStyle: CSSProperties = {
            ...childStyle,
            width: '200%',
            height: '200%',
        };

        return (
            <div style={parentStyle} ref={this.containerRef}>
                <div style={parentStyle} ref={this.expandRef} onScroll={this.handleScroll}>
                    <div style={expandChildStyle} />
                </div>
                <div style={parentStyle} onScroll={this.handleScroll} ref={this.shrinkRef}>
                    <div style={shrinkChildStyle} />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({});
const mapDispatchToProps = (_dispatch: Dispatch) => ({});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(SheetBoxResizeSensor);
