/* eslint-disable prettier/prettier */
import { Action, createAction, handleActions } from 'redux-actions';
import { Reducer } from 'redux';
import { SheetState, Fields, SheetData, FieldsData, SheetModifyRect } from '@dataTypes';
import { FieldItem, SheetItem } from '@sheet/model';
import { ModifyData, MODIFY_TYPE } from '@sheet/model/modify-data';
import { FIELD_TYPE } from '@sheet/components/sheet-table/field-view';
import { getRect } from '@sheet/utils';
import { cLog } from '@debug/logger';
import { FieldModifyData, FIELD_MODIFY_TYPE } from '@sheet/model/field-modify-data';

export const SHEET = 'sheet';

const ADD_SHEET_DATAS = 'ADD_SHEET_DATAS';
const MODIFY_SHEET_DATA = 'MODIFY_SHEET_DATA';

const MODIFY_ROW_CELL = 'MODIFY_ROW_CELL';
const MODIFY_COL_CELL = 'MODIFY_COL_CELL';
const REMOVE_MODIFY = 'REMOVE_MODIFY';

const COPY_CELL = 'COPY_CELL';
const PASTE_CELL = 'PASTE_CELL';
const DELETE_CELL = 'DELETE_CELL';
const CUT_CELL = 'CUT_CELL';

const FIELD_STATE_CHANGE = 'FIELD_STATE_CHANGE';

const RESET_SHEET_STATE = 'RESET_SHEET_STATE';

const fields: Fields = {
    cols: undefined,
    rows: undefined,
};

const initState: SheetState = {
    fields,
    sheetItems: [],
    dragStartItem: undefined,
    dragEndItem: undefined,
    modifyData: undefined,
    fieldRect: undefined,
    fieldModifyData: undefined,

    originData: undefined,
};

const ALPHABET_LEN = 26;

const createFieldNumberStr = (index: number) => (index + 1).toString();
const createFieldAlphabetStr = (index: number) => {
    let val = Number(index + 1);
    let str = '';

    while (val > 0) {
        const a = ((val - 1) / ALPHABET_LEN) | 0;
        const b = (val - 1) % ALPHABET_LEN;
        str = String.fromCharCode(b + 65) + str;
        val = a;
    }

    return str;
};
const createFieldDataStr = (fields: string[] | undefined) => (index: number) => fields ? fields[index] : '-';

let colFieldDataStr: boolean;
let rowFieldDataStr: boolean;

const setSheetData = (state: SheetState, sheetData: SheetData) => {
    const { data, fields } = sheetData;
    const { cols, rows } = fields as FieldsData;

    const colLen = data.length;
    const colFieldItems: FieldItem[] = [];
    const rowFieldItems: FieldItem[] = [];
    const selectItems: SheetItem[][] = [];

    colFieldDataStr = !!(!cols || cols?.length);
    rowFieldDataStr = !!(!rows || rows?.length);

    const createLowFieldStr = colFieldDataStr ? createFieldDataStr(cols) : createFieldNumberStr;
    const createRowFieldStr = rowFieldDataStr ? createFieldDataStr(rows) : createFieldAlphabetStr;

    let addRowField = (rowIndex) => {
        const str = createRowFieldStr(rowIndex);
        rowFieldItems.push(new FieldItem({ str, col: 0, row: rowIndex, type: FIELD_TYPE.TOP }));
    };

    for (let colIndex = 0; colIndex < colLen; colIndex++) {
        const str = createLowFieldStr(colIndex);
        colFieldItems.push(new FieldItem({ str, col: colIndex, row: 0, type: FIELD_TYPE.LEFT }));
        selectItems[colIndex] = [];

        const rowItems = data[colIndex];
        const rowLen = rowItems.length;
        for (let rowIndex = 0; rowIndex < rowLen; rowIndex++) {
            addRowField(rowIndex);

            let sheetItemData = rowItems[rowIndex];
            if (!(sheetItemData === undefined || sheetItemData === null)) {
                sheetItemData = sheetItemData.toString();
            }
            selectItems[colIndex][rowIndex] = new SheetItem({ str: sheetItemData, col: colIndex, row: rowIndex });
        }

        addRowField = (rowIndex) => {
            return rowIndex;
        };
    }

    state.sheetItems = selectItems;
    state.fields.cols = colFieldItems;
    state.fields.rows = rowFieldItems;

    cLog(colFieldItems);

    return state;
};

type Payload = any | string | number | boolean | { x: number; y: number } | SheetData | ModifyData;

const reducer: Reducer<SheetState, Action<Payload>> = handleActions<SheetState, Payload>(
    {
        [ADD_SHEET_DATAS]: (state: SheetState, action: Action<Payload>) => {
            const { sheetData } = action.payload;
            const originData = sheetData;
            setSheetData(state, sheetData);

            return {
                ...state,
                originData,
            };
        },
        [MODIFY_SHEET_DATA]: (state: SheetState, action: Action<Payload>) => {
            const { str, col, row } = action.payload;

            let modifyData = state.modifyData;

            if (state.sheetItems) {
                state.sheetItems[col][row].str = str;
                const modifyItem = state.sheetItems[col][row];

                modifyData = new ModifyData();
                modifyData.type = MODIFY_TYPE.MODIFY;
                modifyData.modifyItem = modifyItem;
            }

            return {
                ...state,
                modifyData,
            };
        },
        [MODIFY_ROW_CELL]: (state: SheetState, action: Action<Payload>) => {
            const { index, type, fieldType, fieldCount, directionType, isFieldOption } = action.payload;
            const sheetItems = state.sheetItems || [];
            const colLen = sheetItems.length;
            let modifyData = state.modifyData;
            let modifyRowField;

            const { rows: fieldRows } = state.fields;
            const { sr = 0, er = 0 } = state.fieldRect || {};
            let fieldIndex;
            if (er - sr <= 0 || isFieldOption) {
                fieldIndex = index;
            } else {
                fieldIndex = directionType === 'prev' ? sr : er + 1;
            }

            switch (type) {
                case MODIFY_TYPE.ADD:
                    if (fieldRows) {
                        const fieldItems: FieldItem[] = [];
                        for (let i = 0; i < fieldCount; i++) {
                            const str = createFieldAlphabetStr(fieldIndex + i);
                            fieldItems.push(new FieldItem({ str, col: 0, row: fieldIndex + i, type: FIELD_TYPE.TOP }));
                        }
                        fieldRows.splice(fieldIndex, 0, ...fieldItems);
                    }

                    modifyRowField = (rowIndex) => {
                        if (fieldRows) {
                            const fieldRow = fieldRows[rowIndex];
                            if (!rowFieldDataStr) {
                                const str = createFieldAlphabetStr(rowIndex);
                                fieldRow.str = str;
                            }

                            fieldRow.row = rowIndex;
                            if (sr !== undefined && er !== undefined)
                                fieldRow.isSelect = rowIndex >= sr && rowIndex <= er;
                        }
                    };

                    for (let i = 0; i < colLen; i++) {
                        const newSheetItems: SheetItem[] = [];
                        for (let j = 0; j < fieldCount; j++) {
                            newSheetItems.push(new SheetItem({ str: '', col: i, row: fieldIndex + j }));
                        }
                        sheetItems[i].splice(fieldIndex, 0, ...newSheetItems);
                        const rows = sheetItems[i];

                        for (let ui = fieldIndex; ui < rows.length; ui++) {
                            rows[ui].row = ui;
                            modifyRowField(ui);
                        }

                        modifyRowField = (rowIndex) => {
                            return rowIndex;
                        };
                    }

                    modifyData = new ModifyData();
                    modifyData.index = fieldIndex;
                    modifyData.type = type;
                    modifyData.fieldType = fieldType;
                    break;

                case MODIFY_TYPE.REMOVE:
                    if (fieldRows) fieldRows.splice(index, 1);

                    modifyRowField = (rowIndex) => {
                        if (fieldRows) {
                            const fieldRow = fieldRows[rowIndex];
                            if (!rowFieldDataStr) {
                                const str = createFieldAlphabetStr(rowIndex);
                                fieldRow.str = str;
                            }

                            fieldRow.row = rowIndex;
                            if (sr !== undefined && er !== undefined)
                                fieldRow.isSelect = rowIndex >= sr && rowIndex <= er;
                        }
                    };

                    for (let i = 0; i < colLen; i++) {
                        sheetItems[i].splice(index, 1);
                        const rows = sheetItems[i];

                        for (let ui = index; ui < rows.length; ui++) {
                            rows[ui].row = ui;
                            modifyRowField(ui);
                        }

                        modifyRowField = (rowIndex) => {
                            return rowIndex;
                        };
                    }

                    modifyData = new ModifyData();
                    modifyData.index = index;
                    modifyData.type = type;
                    modifyData.fieldType = fieldType;
                    break;

                default:
                    break;
            }

            return {
                ...state,
                sheetItems,
                modifyData,
            };
        },
        [MODIFY_COL_CELL]: (state: SheetState, action: Action<Payload>) => {
            const { index, type, fieldType, fieldCount, directionType, isFieldOption } = action.payload;
            const sheetItems = state.sheetItems || [];
            const sheetRowItems = sheetItems[0] || [];
            let colLen;
            let modifyData = state.modifyData;

            let modifyColField;

            const { cols: fieldCols } = state.fields;
            const { sc = 0, ec = 0 } = state.fieldRect || {};
            let fieldIndex;
            if (ec - sc <= 0 || isFieldOption) {
                fieldIndex = index;
            } else {
                fieldIndex = directionType === 'prev' ? sc : ec + 1;
            }

            switch (type) {
                case MODIFY_TYPE.ADD:
                    if (fieldCols) {
                        const fieldItems: FieldItem[] = [];
                        for (let i = 0; i < fieldCount; i++) {
                            const str = createFieldNumberStr(fieldIndex + i);
                            fieldItems.push(new FieldItem({ str, col: fieldIndex + i, row: 0, type: FIELD_TYPE.LEFT }));
                        }
                        fieldCols.splice(fieldIndex, 0, ...fieldItems);
                    }

                    modifyColField = (colIndex) => {
                        if (fieldCols) {
                            const fieldCol = fieldCols[colIndex];
                            if (!colFieldDataStr) {
                                const str = createFieldNumberStr(colIndex);
                                fieldCol.str = str;
                            }

                            fieldCol.col = colIndex;
                            if (sc !== undefined && ec !== undefined)
                                fieldCol.isSelect = colIndex >= sc && colIndex <= ec;
                        }
                    };

                    for (let i = 0; i < fieldCount; i++) {
                        const addRows: SheetItem[] = sheetRowItems.map((_item, i) => {
                            return new SheetItem({ str: '', col: fieldIndex, row: i });
                        });

                        sheetItems.splice(fieldIndex + i, 0, addRows);
                        colLen = sheetItems.length;
                        for (let i = fieldIndex; i < colLen; i++) {
                            modifyColField(i);
                            const rows = sheetItems[i];
                            for (let ui = 0; ui < rows.length; ui++) rows[ui].col = i;
                        }
                    }

                    modifyData = new ModifyData();
                    modifyData.index = fieldIndex;
                    modifyData.type = type;
                    modifyData.fieldType = fieldType;
                    break;

                case MODIFY_TYPE.REMOVE:
                    if (fieldCols) fieldCols.splice(index, 1);

                    modifyColField = (colIndex) => {
                        if (fieldCols) {
                            const fieldCol = fieldCols[colIndex];
                            if (!colFieldDataStr) {
                                const str = createFieldNumberStr(colIndex);
                                fieldCol.str = str;
                            }

                            fieldCol.col = colIndex;
                            if (sc !== undefined && ec !== undefined)
                                fieldCol.isSelect = colIndex >= sc && colIndex <= ec;
                        }
                    };

                    sheetItems.splice(index, 1);

                    colLen = sheetItems.length;
                    for (let i = index; i < colLen; i++) {
                        modifyColField(i);
                        const rows = sheetItems[i];

                        for (let ui = 0; ui < rows.length; ui++) rows[ui].col = i;
                    }

                    modifyData = new ModifyData();
                    modifyData.index = index;
                    modifyData.type = type;
                    modifyData.fieldType = fieldType;
                    break;

                default:
                    break;
            }

            return {
                ...state,
                sheetItems,
                modifyData,
            };
        },

        [COPY_CELL]: (state: SheetState, _action: Action<Payload>) => {
            return {
                ...state,
            };
        },

        [PASTE_CELL]: (state: SheetState, action: Action<Payload>) => {
            const { start, end, type, text } = action.payload;
            const fieldRect = state.fieldRect as SheetModifyRect;
            const sheetItems = state.sheetItems;
            const rect: SheetModifyRect | null = getRect(start, end);
            let modifyData;

            if (rect && sheetItems) {
                const { sc = 0, sr = 0, ec = 0, er = 0 } = fieldRect || {};
                const rows = text.split('\n');
                let start;
                let end;
                rows.forEach((row, colIndex) => {
                    const cells = row.split('\t');
                    cells.forEach((cellData, rowIndex) => {
                        const col = sc + colIndex;
                        const row = sr + rowIndex;
                        if (sheetItems.length <= col || sheetItems[0].length <= row) {
                            return;
                        }
                        end = sheetItems[col][row];
                        end.str = cellData;
                        if (!start) {
                            start = end;
                        }
                    });
                });

                modifyData = new ModifyData();
                modifyData.type = type;
                modifyData.start = start;
                modifyData.end = end;
                modifyData.rect = rect;
            }

            return {
                ...state,
                sheetItems,
                modifyData,
            };
        },

        [DELETE_CELL]: (state: SheetState, action: Action<Payload>) => {
            const { start, end, type } = action.payload;
            const rect: SheetModifyRect | null = getRect(start, end);
            let modifyData;

            if (rect) {
                const sheetItems = state.sheetItems || [];
                const { sc, ec, sr, er } = rect;

                for (let ci = sc; ci <= ec; ci++) {
                    const rows = sheetItems[ci];
                    for (let ri = sr; ri <= er; ri++) rows[ri].str = '';
                }

                modifyData = new ModifyData();
                modifyData.type = type;
                modifyData.start = start;
                modifyData.end = end;
                modifyData.rect = rect;
            }

            return {
                ...state,
                modifyData,
            };
        },

        [CUT_CELL]: (state: SheetState, action: Action<Payload>) => {
            const { start, end, modifyItem, type } = action.payload;
            const { col: mCol, row: mRow } = modifyItem;
            const rect: SheetModifyRect | null = getRect(start, end);
            let modifyData;

            if (rect) {
                const cutRowLen = rect.er - rect.sr;
                const cutColLen = rect.ec - rect.sc;

                const sheetItems = state.sheetItems || [];
                const sheetRowItems = sheetItems[0] || [];
                const colLastIndex = sheetItems.length - 1;
                const rowLastIndex = sheetRowItems.length - 1;
                const cutColLastIndex = mCol + cutColLen;
                const cutRowLastIndex = mRow + cutRowLen;

                const { cols: fieldCols, rows: fieldRows } = state.fields;
                let addColCnt = 0;
                let addRowCnt = 0;

                // col 
                if (cutColLastIndex > colLastIndex) {
                    addColCnt = cutColLastIndex - colLastIndex;
                    for (let ci = colLastIndex + 1; ci <= cutColLastIndex; ci++) {
                        if (fieldCols) {
                            const str = createFieldNumberStr(ci);
                            fieldCols.push(new FieldItem({ str, col: ci, row: 0, type: FIELD_TYPE.LEFT }));
                        }

                        const addRows: SheetItem[] = sheetRowItems.map((_item, i) => {
                            return new SheetItem({ str: '', col: ci, row: i });
                        });

                        sheetItems.push(addRows);
                    }
                }
                // row 
                if (cutRowLastIndex > rowLastIndex) {
                    if (fieldRows) {
                        for (let fi = rowLastIndex + 1; fi <= cutRowLastIndex; fi++) {
                            const str = createFieldAlphabetStr(fi);
                            fieldRows.push(new FieldItem({ str, col: 0, row: fi, type: FIELD_TYPE.TOP }));
                        }
                    }

                    const colLen = sheetItems.length;
                    addRowCnt = cutRowLastIndex - rowLastIndex;

                    for (let i = 0; i < colLen; i++) {
                        for (let ui = rowLastIndex + 1; ui <= cutRowLastIndex; ui++) {
                            sheetItems[i].push(new SheetItem({ str: '', col: i, row: ui }));
                        }
                    }
                }

                const cutData: any[][] = [];
                //  cut
                for (let i = 0; i <= cutColLen; i++) {
                    cutData[i] = [];
                    const cRows = sheetItems[rect.sc + i];
                    for (let ri = 0; ri <= cutRowLen; ri++) {
                        const cutItem = cRows[rect.sr + ri];
                        cutData[i][ri] = cutItem.str;
                        cutItem.str = '';
                    }
                }

                //  push
                for (let i = 0; i <= cutColLen; i++) {
                    const mRows = sheetItems[mCol + i];
                    for (let ri = 0; ri <= cutRowLen; ri++) {
                        const modifyItem = mRows[mRow + ri];
                        modifyItem.str = cutData[i][ri];
                    }
                }

                modifyData = new ModifyData();
                modifyData.type = type;
                modifyData.start = start;
                modifyData.end = end;
                modifyData.rect = rect;
                modifyData.modifyItem = modifyItem;
                modifyData.addColCnt = addColCnt;
                modifyData.addRowCnt = addRowCnt;
            }

            return {
                ...state,
                modifyData,
            };
        },

        [FIELD_STATE_CHANGE]: (state: SheetState, action: Action<Payload>) => {
            const fieldRect = action.payload.fieldRect;
            let fieldModifyData = state.fieldModifyData;
            // let modifyData = state.modifyData;

            cLog(fieldRect);

            if (fieldRect) {
                const { sc, ec, sr, er, standardItem } = fieldRect;
                if (state.fieldRect) {
                    const { sc: osc, ec: oec, sr: osr, er: oer } = state.fieldRect;
                    if (osc === sc && oec === ec && osr === sr && oer === er) {
                        return { ...state };
                    }
                }

                const { fields } = state;
                const { cols, rows } = fields;

                if (rows)
                    rows.forEach((rowItem) => {
                        const { row } = rowItem;
                        if (row !== undefined) {
                            rowItem.isSelect = row >= sr && row <= er;
                            rowItem.isSelectAll = false;
                        }
                    });

                if (cols)
                    cols.forEach((colItem) => {
                        const { col } = colItem;
                        if (col !== undefined) {
                            colItem.isSelect = col >= sc && col <= ec;
                            colItem.isSelectAll = false;
                        }
                    });

                if (standardItem) {
                    const { col: standardItemCol, row: standardItemRow, type } = standardItem;
                    if (type === FIELD_TYPE.LEFT) {
                        if (cols) cols[standardItemCol].isSelectAll = true;
                    } else {
                        if (rows) rows[standardItemRow].isSelectAll = true;
                    }
                }

                // modifyData = new ModifyData();
                // modifyData.type = MODIFY_TYPE.FIELD_STATE;

                fieldModifyData = new FieldModifyData();
                fieldModifyData.type = FIELD_MODIFY_TYPE.FIELD_STATE;
            } else {
                if (state.fieldRect) {
                    cLog('reset');
                    const { fields } = state;
                    const { cols, rows } = fields;

                    if (rows)
                        rows.forEach((rowItem) => {
                            const { row } = rowItem;
                            if (row !== undefined) {
                                rowItem.isSelect = false;
                                rowItem.isSelectAll = false;
                            }
                        });

                    if (cols)
                        cols.forEach((colItem) => {
                            const { col } = colItem;
                            if (col !== undefined) {
                                colItem.isSelect = false;
                                colItem.isSelectAll = false;
                            }
                        });

                    // modifyData = new ModifyData();
                    // modifyData.type = MODIFY_TYPE.FIELD_STATE;
                    fieldModifyData = new FieldModifyData();
                    fieldModifyData.type = FIELD_MODIFY_TYPE.FIELD_STATE;
                }
                // fieldRect = state.fieldRect;
            }

            return {
                ...state,
                fieldModifyData,
                fieldRect,
                // modifyData,
            };
        },

        [RESET_SHEET_STATE]: (_state: SheetState, _action: Action<Payload>) => {
            const newState = {
                fields: { cols: undefined, rows: undefined },
                sheetItems: [],
                dragStartItem: undefined,
                dragEndItem: undefined,
                modifyData: undefined,
                fieldRect: undefined,
                originData: undefined,
                fieldModifyData: undefined,
            };
            return {
                ...newState,
            };
        },
    },
    initState,
);

export const sheetActions = {
    addSheetDatas: createAction<Payload>(ADD_SHEET_DATAS),
    modifySheetData: createAction<Payload>(MODIFY_SHEET_DATA),

    modifyRowCell: createAction<Payload>(MODIFY_ROW_CELL),
    modifyColCell: createAction<Payload>(MODIFY_COL_CELL),
    removeModify: createAction<Payload>(REMOVE_MODIFY),

    copyCell: createAction<Payload>(COPY_CELL),
    pasteCell: createAction<Payload>(PASTE_CELL),
    deleteCell: createAction<Payload>(DELETE_CELL),
    cutCell: createAction<Payload>(CUT_CELL),

    fieldStateChange: createAction<Payload>(FIELD_STATE_CHANGE),
    resetSheetState: createAction<Payload>(RESET_SHEET_STATE),
};

export default reducer;
