/* eslint-disable no-useless-constructor */
/* eslint-disable no-empty-function */
import { IBaseItem, SheetModifyData, SheetModifyRect } from '@dataTypes';
import { FIELD_TYPE } from '@sheet/components/sheet-table/field-view';

export enum MODIFY_TYPE {
    ADD = 'MODIFY_TYPE_ADD',
    REMOVE = 'MODIFY_TYPE_REMOVE',
    MODIFY = 'MODIFY_TYPE_MODIFY',
    COPY = 'MODIFY_TYPE_COPY',
    PASTE = 'MODIFY_TYPE_PASTE',
    DELETE = 'MODIFY_TYPE_DELETE',
    CUT = 'MODIFY_TYPE_CUT',
    FIELD_STATE = 'MODIFY_TYPE_FIELD_STATE',
}

export class ModifyData implements SheetModifyData {
    type?: MODIFY_TYPE;
    text?: string;

    start?: IBaseItem;
    end?: IBaseItem;
    standardItem?: IBaseItem;

    index?: number;
    fieldType?: FIELD_TYPE | string;
    fieldCount?: number;

    modifyItem?: IBaseItem;
    addColCnt?: number;
    addRowCnt?: number;
    rect?: SheetModifyRect;

    directionType?: 'prev' | 'next';
    isFieldOption?: boolean;

    constructor() {}
}
