import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import config from '@sheet/config';
import { StoreState } from '@dataTypes';

const selectRect = (state: StoreState) => state.editor.rect;
const TABLE_SHEET_SIZE = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const { CELL_WIDTH, CELL_HEIGHT } = TABLE_SHEET_SIZE;

type CellDragSelectProps = {};

const CellDragSelect = (_props: CellDragSelectProps) => {
    const rect = useSelector(selectRect);

    useEffect(() => {
        return () => {};
    }, []);

    const [positionRect, setPositionRect] = useState<any>({ display: 'none' });

    useEffect(() => {
        if (rect) {
            const { sc, ec, sr, er } = rect;

            if (sc === ec && sr === er) {
                setPositionRect(undefined);
            } else {
                const left = `${CELL_WIDTH * (sr || 0)}px`;
                const top = `${CELL_HEIGHT * (sc || 0)}px`;
                const width = `${(er - sr + 1) * CELL_WIDTH}px`;
                const height = `${(ec - sc + 1) * CELL_HEIGHT}px`;

                setPositionRect({
                    left,
                    top,
                    width,
                    height,
                    pointerEvents: 'none',
                    display: '',
                });
            }
        } else {
            setPositionRect(undefined);
        }
    }, [rect]);

    return positionRect ? <div className={Styles.cell_drag_select} style={positionRect} /> : <></>;
};

export default CellDragSelect;
