/* eslint-disable react/no-direct-mutation-state */
/* eslint-disable react/no-deprecated */
import React from 'react';
import { Dispatch, connect } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { StoreState } from '@dataTypes';
import config from '@sheet/config';

import { setCaretToPos } from '@sheet/utils';
import { cLog } from '@debug/logger';

const TABLE_SHEET_SIZE = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const EDITOR_OPTION = config.EDITOR;
const { CELL_WIDTH, CELL_HEIGHT } = TABLE_SHEET_SIZE;
const { MAX_TEXT } = EDITOR_OPTION;

type CellEditProps = {
    defaultValue?: string | number;
    defaultCursor?: number;
    updateText?: (text?: string) => void;
};

interface CellEditState {
    textareaValue?: string | number;
    position?: { width?: string; height?: string };
}

class CellEdit extends React.Component<CellEditProps, CellEditState> {
    state: CellEditState = {
        textareaValue: '',
        position: undefined,
    };

    textareaRef: React.RefObject<HTMLTextAreaElement>;

    // eslint-disable-next-line no-useless-constructor
    constructor(props: CellEditProps) {
        super(props);
        const { defaultValue } = props;

        this.state = {
            textareaValue: defaultValue === undefined ? '' : defaultValue,
        };

        this.textareaRef = React.createRef();
    }

    componentWillUnmount() {
        this.textareaRef.current?.removeEventListener('keydown', this._onKeyDown);
    }

    componentWillMount() {
        cLog('[CellEdit] componentWillMount');
    }

    componentDidMount() {
        cLog('[CellEdit] componentDidMount');
        if (this.textareaRef.current) {
            const { defaultValue, defaultCursor } = this.props;
            const cursor = defaultCursor === undefined ? defaultValue?.toString().length || 0 : defaultCursor;
            setCaretToPos(this.textareaRef.current, cursor);

            this.textareaRef.current.addEventListener('keydown', this._onKeyDown);
        }
    }

    componentDidUpdate() {
        cLog('[CellEdit] componentDidUpdate');
        this.focus(false);
    }

    getValue() {
        return this.state.textareaValue;
    }

    cancelValue(textareaValue) {
        this.setState({
            textareaValue,
        });
    }

    updateSize(size: { width: number; height: number }, text?: string | number) {
        const { width: updateWidth, height: updateHeight } = size;
        const width = `${updateWidth < CELL_WIDTH + 1 ? CELL_WIDTH + 1 : updateWidth}px`;
        const height = `${updateHeight < CELL_HEIGHT + 1 ? CELL_HEIGHT + 1 : updateHeight}px`;

        this.setState({
            position: { width, height },
            textareaValue: text,
        });
    }

    focus(isReset = true) {
        if (this.textareaRef.current) {
            if (isReset) {
                this.textareaRef.current.value = '';
                this.state.textareaValue = '';
            }
            this.textareaRef.current.focus();
        }
    }

    _onKeyDown = (_e) => {
        // cLog(e);
        // e.preventDefault();
    };

    _handleOnChange = (e) => {
        // this.setState({
        //     textareaValue: e.target.value,
        // });
        const value = e.target.value;

        if (value.length <= MAX_TEXT) {
            const { updateText } = this.props;
            if (updateText) updateText(value);
        } else {
            cLog('MAX_TEXT');
        }
    };

    render() {
        const { textareaValue, position } = this.state;
        const style = position || {};

        return (
            <div className={Styles.cell_edit} style={style}>
                <textarea
                    ref={this.textareaRef}
                    id="edit"
                    name="edit"
                    value={textareaValue?.toString()}
                    onChange={this._handleOnChange}></textarea>
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // modifyItem: (modifyCellItem) => dispatch(modifyItem(modifyCellItem)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(CellEdit);
