/* eslint-disable react/no-direct-mutation-state */
/* eslint-disable react/no-deprecated */
import React from 'react';
import { Dispatch, connect } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { StoreState } from '@dataTypes';
import { cLog } from '@debug/logger';

type CellHideProps = {
    position?: { minWidth?: string; maxWidth?: string };
    defaultValue?: string | number;
    updateSize?: (size: { width: number; height: number }, text?: string | number) => void;
};

interface CellHideState {
    textareaValue?: string | number;
    position?: { minWidth?: string; maxWidth?: string };
}

class CellHide extends React.Component<CellHideProps, CellHideState> {
    hideRef: React.RefObject<HTMLDivElement>;

    state: CellHideState = {
        textareaValue: '',
        position: undefined,
    };

    // eslint-disable-next-line no-useless-constructor
    constructor(props: CellHideProps) {
        super(props);

        this.hideRef = React.createRef();
        const { position, defaultValue } = props;

        this.state = {
            position,
            textareaValue: defaultValue,
        };
    }

    componentWillUnmount() {}

    componentWillMount() {
        cLog('[CellHide] componentWillMount');
    }

    componentDidMount() {
        cLog('[CellHide] componentDidMount');
        this._updataSize();
    }

    componentDidUpdate() {
        cLog('[CellHide] componentDidUpdate');
        this._updataSize();
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        this.setState({
            textareaValue: nextProps.defaultValue || '',
            position: nextProps.position,
        });
    }

    updateText(text?: string) {
        this.setState({
            textareaValue: text || '',
        });
    }

    _updataSize = () => {
        const hide = this.hideRef.current;
        if (hide) {
            const { updateSize } = this.props;
            const { textareaValue } = this.state;
            if (updateSize)
                updateSize(
                    {
                        width: hide.offsetWidth + 1,
                        height: hide.offsetHeight,
                    },
                    textareaValue,
                );
        }
    };

    render() {
        const { textareaValue, position } = this.state;
        const style = position || {};

        return (
            <div ref={this.hideRef} className={Styles.hide} style={style}>
                {/*    textarea  */}
                {textareaValue}
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // modifyItem: (modifyCellItem) => dispatch(modifyItem(modifyCellItem)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(CellHide);
