import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectSelectCell = (state: StoreState) => state.editor.selectCell;

type SelectCellHookProps = {
    changeSelectCell?: (selectCell) => void;
};

const SelectCellHook = (props: SelectCellHookProps) => {
    const selectCell = useSelector(selectSelectCell);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeSelectCell } = props || {};
        if (changeSelectCell) changeSelectCell(selectCell);
    }, [selectCell]);

    return <></>;
};

export default SelectCellHook;
