import { IBaseItem } from '@dataTypes';

export class FieldItem implements IBaseItem {
    type?: string;
    str?: string | number;
    col?: number;
    row?: number;
    isSelect?: boolean;
    isSelectAll?: boolean;

    constructor(fieldItemData: { str?: string | number; col?: number; row?: number; type?: string }) {
        this.str = fieldItemData.str;
        this.col = fieldItemData.col;
        this.row = fieldItemData.row;
        this.type = fieldItemData.type;
    }
}
