import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import config from '@sheet/config';
import { StoreState } from '@dataTypes';

const selectModifyRect = (state: StoreState) => state.editor.modifyRect;
const TABLE_SHEET_SIZE = config.SHEET_TABLE.TABLE_SHEET_SIZE;
const { CELL_WIDTH, CELL_HEIGHT } = TABLE_SHEET_SIZE;

type CellDashedSelectProps = {};

const CellDashedSelect = (_props: CellDashedSelectProps) => {
    const modifyRect = useSelector(selectModifyRect);

    useEffect(() => {
        return () => {};
    }, []);

    const [positionRect, setPositionRect] = useState<any>({ display: 'none' });

    useEffect(() => {
        if (modifyRect) {
            const { sc, ec, sr, er } = modifyRect;
            const MARGIN_POSITION = 1;
            const MARGIN_SZIE = 3;

            const left = `${CELL_WIDTH * (sr || 0) - MARGIN_POSITION}px`;
            const top = `${CELL_HEIGHT * (sc || 0) - MARGIN_POSITION}px`;
            const width = `${(er - sr + 1) * CELL_WIDTH + MARGIN_SZIE}px`;
            const height = `${(ec - sc + 1) * CELL_HEIGHT + MARGIN_SZIE}px`;

            setPositionRect({
                left,
                top,
                width,
                height,
                pointerEvents: 'none',
                display: '',
            });
        } else {
            setPositionRect(undefined);
        }
    }, [modifyRect]);

    return positionRect ? <div className={Styles.cell_dashed_select} style={positionRect} /> : <></>;
};

export default CellDashedSelect;
