import { Action, createAction, handleActions } from 'redux-actions';
import { Reducer } from 'redux';
import { EditorState } from '@dataTypes';
import { getRect } from '@sheet/utils';
import { ModifyData } from '@sheet/model/modify-data';
import { EditorModifyData } from '@sheet/model/editor-modify-data';
import { EditData } from '@sheet/model/edit-data';

const SELECT_CELL = 'SELECT_CELL';
const SELECT_START_CELL = 'SELECT_START_CELL';
const SELECT_END_CELL = 'SELECT_END_CELL';

const FIELD_SELECT_CELL = 'FIELD_SELECT_CELL';
const FIELD_SELECT_END_CELL = 'FIELD_SELECT_END_CELL';

const MODIFY_DATA = 'MODIFY_DATA';
const MODIFY_RECT = 'MODIFY_RECT';

const RESET_EDITOR_STATE = 'RESET_EDITOR_STATE';

const SET_EDIT = 'SET_EDIT';
const EDIT_COMPLETE = 'EDIT_COMPLETE';

const SET_VIEW_PORT = 'SET_VIEW_PORT';
const SET_REAL_VIEW_PORT = 'SET_REAL_VIEW_PORT';

const CLICK_COUNT = 'CLICK_COUNT';

const initState: EditorState = {
    time: new Date().getTime(),
    selectCell: undefined,
    startCell: undefined,
    endCell: undefined,
    rect: undefined,
    modifyData: undefined,

    // field on   
    fieldRect: undefined,

    // / 
    modifyRect: undefined,

    //   
    editData: undefined,
    isKeyBord: undefined,

    viewPort: undefined,
    realViewPort: undefined,

    clickCount: 0,
};

type Payload = any | string | number | boolean | { x: number; y: number } | ModifyData | EditorModifyData;

const reducer: Reducer<EditorState, Action<Payload>> = handleActions<EditorState, Payload>(
    {
        [SELECT_CELL]: (state: EditorState, action: Action<Payload>) => {
            const { selectCell, isKeyBord } = action.payload;
            const { editData } = state;
            const clickCount = ((state.clickCount || 0) + 1) % 100000;
            const startCell = selectCell;
            const endCell = undefined;
            const rect = undefined;

            const fieldRect = getRect(selectCell, selectCell);

            return {
                ...state,
                selectCell,
                startCell,
                endCell,
                rect,
                fieldRect,
                editData: undefined,
                isKeyBord,
                clickCount,
            };
        },

        [SELECT_START_CELL]: (state: EditorState, action: Action<Payload>) => {
            const startCell = action.payload;

            return {
                ...state,
                startCell,
            };
        },

        [SELECT_END_CELL]: (state: EditorState, action: Action<Payload>) => {
            const endCell = action.payload;
            const rect = state.startCell ? getRect(state.startCell, endCell) : undefined;
            const fieldRect = rect;

            return {
                ...state,
                endCell,
                rect,
                fieldRect,
            };
        },

        [FIELD_SELECT_CELL]: (state: EditorState, action: Action<Payload>) => {
            const { start, end, standardItem } = action.payload || {};
            const isKeyBord = undefined;
            const startCell = start;
            const selectCell = start;
            const endCell = end;
            const rect = getRect(start, end);
            const fieldRect = rect;
            if (standardItem && fieldRect) fieldRect.standardItem = standardItem;
            const editData = undefined;

            return {
                ...state,
                selectCell,
                startCell,
                endCell,
                rect,
                fieldRect,
                editData,
                isKeyBord,
            };
        },

        [FIELD_SELECT_END_CELL]: (state: EditorState, action: Action<Payload>) => {
            const { end, standardItem } = action.payload || {};
            const { startCell } = state || {};
            const isKeyBord = undefined;
            const endCell = end;
            const rect = getRect(startCell, end);

            const fieldRect = rect;
            if (standardItem && fieldRect) fieldRect.standardItem = standardItem;
            const editData = undefined;

            return {
                ...state,
                endCell,
                rect,
                fieldRect,
                editData,
                isKeyBord,
            };
        },

        [MODIFY_DATA]: (state: EditorState, action: Action<Payload>) => {
            const modifyData = action.payload;

            return {
                ...state,
                modifyData,
            };
        },

        [MODIFY_RECT]: (state: EditorState, action: Action<Payload>) => {
            const type = action.payload;
            let modifyRect;

            if (type) {
                const { startCell, rect } = state;
                if (rect) {
                    const { sc, ec, sr, er } = rect;
                    modifyRect = { type, sc, ec, sr, er };
                } else {
                    modifyRect = getRect(startCell, startCell);
                    if (modifyRect) modifyRect.type = type;
                }
            }

            return {
                ...state,
                modifyRect,
            };
        },

        [RESET_EDITOR_STATE]: (_state: EditorState, _action: Action<Payload>) => {
            const newState = {
                time: new Date().getTime(),
                selectCell: undefined,
                startCell: undefined,
                endCell: undefined,
                rect: undefined,
                fieldRect: undefined,
                modifyRect: undefined,
                modifyData: undefined,
                editData: undefined,
                isKeyBord: undefined,
                viewPort: undefined,
                realViewPort: undefined,
                clickCount: 0,
            };

            return {
                ...newState,
            };
        },

        [SET_EDIT]: (state: EditorState, action: Action<Payload>) => {
            const { defaultText, cursor, isCancel } = action.payload || {};
            const { time, selectCell } = state;

            const editData = new EditData();
            editData.time = time;
            editData.defaultText = defaultText;
            editData.editItem = selectCell;
            editData.cursor = cursor;
            editData.isCancel = isCancel;

            const startCell = undefined;
            const endCell = undefined;
            const rect = undefined;
            const modifyData = undefined;
            const modifyRect = undefined;
            // const viewPort = undefined;
            const fieldRect = getRect(selectCell, selectCell);

            return {
                ...state,
                fieldRect,
                startCell,
                endCell,
                rect,
                modifyData,
                modifyRect,
                editData,
            };
        },

        [SET_VIEW_PORT]: (state: EditorState, action: Action<Payload>) => {
            const viewPort = action.payload;

            return {
                ...state,
                viewPort,
            };
        },
        [SET_REAL_VIEW_PORT]: (state: EditorState, action: Action<Payload>) => {
            const realViewPort = action.payload;

            return {
                ...state,
                realViewPort,
            };
        },

        [CLICK_COUNT]: (state: EditorState, _action: Action<Payload>) => {
            const clickCount = ((state.clickCount || 0) + 1) % 100000;

            return {
                ...state,
                clickCount,
            };
        },
    },
    initState,
);

export const editorActions = {
    selectCell: createAction<Payload>(SELECT_CELL),
    selectStartCell: createAction<Payload>(SELECT_START_CELL),
    selectEndCell: createAction<Payload>(SELECT_END_CELL),
    fieldSelectCell: createAction<Payload>(FIELD_SELECT_CELL),
    fieldSelectEndCell: createAction<Payload>(FIELD_SELECT_END_CELL),
    editorModifyData: createAction<Payload>(MODIFY_DATA),
    modifyRect: createAction<Payload>(MODIFY_RECT),
    resetEditorState: createAction<Payload>(RESET_EDITOR_STATE),

    setEdit: createAction<Payload>(SET_EDIT),
    editComplete: createAction<Payload>(EDIT_COMPLETE),
    setViewPort: createAction<Payload>(SET_VIEW_PORT),
    setRealViewPort: createAction<Payload>(SET_REAL_VIEW_PORT),

    addClickCount: createAction<Payload>(CLICK_COUNT),
};

export default reducer;
