import { IBaseItem, IEditorModifyData } from '@dataTypes';
import { FIELD_TYPE } from '@sheet/components/sheet-table/field-view';

export enum EDITOR_MODIFY_TYPE {
    CELL = 'EDITOR_MODIFY_TYPE_CELL',
    FIELD = 'EDITOR_MODIFY_TYPE_FIELD',
}

export class EditorModifyData implements IEditorModifyData {
    modifyItem?: IBaseItem;
    type?: EDITOR_MODIFY_TYPE;
    fieldType?: FIELD_TYPE;
    position?: { x: number; y: number; width?: number; height?: number };
    positionCSS?: { left: string; top: string; width?: string; height?: string };
    isLine?: boolean;
    openOption?: boolean;
    over?: boolean;
    fieldCount?: number;

    clone() {
        const editorModifyData = new EditorModifyData();
        editorModifyData.modifyItem = this.modifyItem;
        editorModifyData.type = this.type;
        editorModifyData.fieldType = this.fieldType;
        editorModifyData.position = this.position;
        editorModifyData.positionCSS = this.positionCSS;
        editorModifyData.isLine = this.isLine;
        editorModifyData.openOption = this.openOption;
        editorModifyData.over = this.over;
        editorModifyData.fieldCount = this.fieldCount;
        return editorModifyData;
    }
}
