import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { StoreState } from '@dataTypes';

const selectClickCount = (state: StoreState) => state.editor.clickCount;

type ClickCountHookProps = {
    changeClickCount?: (clickCount) => void;
};

const ClickCountHook = (props: ClickCountHookProps) => {
    const clickCount = useSelector(selectClickCount);

    useEffect(() => {
        return () => {};
    }, []);

    useEffect(() => {
        const { changeClickCount } = props || {};
        if (changeClickCount) changeClickCount(clickCount);
    }, [clickCount]);

    return <></>;
};

export default ClickCountHook;
