/* eslint-disable react/no-deprecated */
/* eslint-disable react/no-string-refs */
import React from 'react';
import { Provider } from 'react-redux';
import StoreManager from './helper/store-manager';
import SheetContainer from './container/sheet-container';
import { IBaseItem, SheetData, SheetOption } from '@dataTypes';
import { ModifyData } from './model/modify-data';
import { editorActions } from './reducers/editor-reducer';
import { sheetActions } from './reducers/sheet-reducer';
import { getSaveData } from './utils';
import { cLog } from '@debug/logger';
import config from './config';

const { resetEditorState } = editorActions;
const { resetSheetState } = sheetActions;

declare let window;
window.EntrySheetVersion = 'v1.0.0.0';

export const ColumnEditType = {
    ADD: 'ADD',
    REMOVE: 'REMOVE',
};

export const RowEditType = {
    ADD: 'ADD',
    REMOVE: 'REMOVE',
};

export const SheetOptionType = {
    EDITOR: 'EDITOR',
    VIEWER: 'VIEWER',
};

export type EntrySheetComponentProps = {
    sheetData?: SheetData;
    option?: SheetOption;
};

interface EntrySheetComponentState {
    renderFlag: boolean;
    sheetData?: SheetData;
    option?: SheetOption;
}

export default class EntrySheetComponent extends React.Component<EntrySheetComponentProps, EntrySheetComponentState> {
    state = {
        renderFlag: false,
        sheetData: undefined,
        option: undefined,
    };

    sheetContainerRef: React.RefObject<typeof SheetContainer>;
    _store;

    constructor(props: EntrySheetComponentProps) {
        super(props);
        this.sheetContainerRef = React.createRef();

        // const reducer = getReducers();
        // this._store = useLogger ? createStore(reducer, applyMiddleware(createLogger())) : createStore(reducer);
    }

    UNSAFE_componentWillMount() {
        cLog('[EntrySheetComponent] UNSAFE_componentWillMount');
        const { sheetData, option } = this.props;
        const { config: optionConfig } = option || {};
        const { maxCell, language } = optionConfig || {};

        if (maxCell) config.MAX_CELL = maxCell;

        if (language) {
            config.LANGUAGE = {
                ...config.LANGUAGE,
                ...language,
            };
        }

        this.setState({
            ...this.state,
            sheetData,
            option,
        });
    }

    componentWillUnmount() {
        cLog('[EntrySheetComponent] componentWillUnmount');
    }

    componentDidMount() {
        cLog('[EntrySheetComponent] componentDidMount');
    }

    // eslint-disable-next-line react/no-deprecated
    componentWillUpdate() {
        cLog('[EntrySheetComponent] componentWillUpdate');
        cLog(StoreManager.state);

        StoreManager.store.dispatch(resetEditorState());
        StoreManager.store.dispatch(resetSheetState());
    }

    componentWillReceiveProps(_nextProps: EntrySheetComponentProps) {
        cLog('[EntrySheetComponent] componentWillReceiveProps');

        const { sheetData, option } = _nextProps;
        const { config: optionConfig } = option || {};
        const { maxCell, language } = optionConfig || {};

        if (maxCell) config.MAX_CELL = maxCell;
        if (language) {
            config.LANGUAGE = {
                ...config.LANGUAGE,
                ...language,
            };
        }

        this.setState({
            ...this.state,
            sheetData,
            option,
        });
    }

    componentDidUpdate() {
        cLog('[EntrySheetComponent] componentDidUpdate');
    }

    getSheetData = () => {
        const sheetData = getSaveData();
        return sheetData;
    };

    setSheetData = (sheetData?: SheetData) => {
        const renderFlag = !this.state.renderFlag;

        this.setState({
            ...this.state,
            renderFlag,
            sheetData,
        });
    };

    testChangeSheetData(sheetData: IBaseItem) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testChangeSheetData(sheetData);
    }

    testModifyRowCell(modifyData: ModifyData) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testModifyRowCell(modifyData);
    }

    testModifyColCell(modifyData: ModifyData) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testModifyColCell(modifyData);
    }

    testCopyCell(modifyData: ModifyData) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testCopyCell(modifyData);
    }

    testDeleteCell(modifyData: ModifyData) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testDeleteCell(modifyData);
    }

    testCutCell(modifyData: ModifyData) {
        if (this.sheetContainerRef.current) this.sheetContainerRef.current.testCutCell(modifyData);
    }

    render() {
        const { sheetData, option } = this.state;

        console.log('StoreManager.store', StoreManager.store);
        console.log('Provider', Provider);

        return (
            <Provider store={StoreManager.store}>
                <SheetContainer
                    key={`SheetContainer__${new Date().getTime()}`}
                    ref={this.sheetContainerRef}
                    sheetData={sheetData}
                    option={option}
                />
            </Provider>
        );
    }
}
