import React, { useEffect, useState } from 'react';
import Styles from '@assets/scss/TableSheet.module.scss';
import { numberWithCommas } from '@sheet/utils';
import config from '@sheet/config';

type ErrorDimmedProps = {
    isError?: boolean;
    onSaveExcel?: () => void;
};

const ErrorDimmed = (props: ErrorDimmedProps) => {
    useEffect(() => {
        return () => {};
    }, []);

    const onSaveExcel = (e) => {
        e.preventDefault();
        const { onSaveExcel } = props;
        if (onSaveExcel) onSaveExcel();
    };

    const [maxCell, setMaxCell] = useState('10,000');

    const [isError, setError] = useState(false);
    useEffect(() => {
        if (props.isError) {
            setMaxCell(numberWithCommas(config.MAX_CELL));
            setError(props.isError);
        }
    }, [props.isError]);

    const text = config.LANGUAGE.maxCellPopup.replace('%s', maxCell);

    return isError ? (
        <div className={Styles.error_dimmed}>
            <div className={Styles.inner}>
                <p>
                    {text.split('<br />').map((line, i) => {
                        return (
                            <span key={i}>
                                {line}
                                <br />
                            </span>
                        );
                    })}
                    {/* <span>
                        {`  ${maxCell} `}
                        <br />
                    </span>
                    <span>   .</span> */}
                </p>
                <a href="/" role="button" className={Styles.btn_download} onClick={onSaveExcel}>
                    {config.LANGUAGE.maxCellPopupDown}
                </a>
            </div>
        </div>
    ) : (
        <></>
    );
};

export default ErrorDimmed;
