import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Styles from '@assets/scss/TableSheet.module.scss';
import { sheetActions } from '@sheet/reducers/sheet-reducer';
import { getCellPositionCSS } from '@sheet/utils';

const { fieldStateChange } = sheetActions;

const selectSelectCell = (state) => state.editor.selectCell;
const selectFieldRect = (state) => state.editor.fieldRect;

type CellSelectProps = {};

const CellSelect = (_props: CellSelectProps) => {
    const selectCell = useSelector(selectSelectCell);
    const fieldRect = useSelector(selectFieldRect);
    const dispatch = useDispatch();

    useEffect(() => {
        return () => {};
    }, []);

    const [positionRect, setPositionRect] = useState<any>({ display: 'none' });

    useEffect(() => {
        if (selectCell) {
            const postionCSS = getCellPositionCSS(selectCell, 0, 0);
            const width = '91px';
            const height = '32px';
            const display = '';

            setPositionRect({
                ...postionCSS,
                width,
                height,
                display,
            } as React.CSSProperties);
        }
    }, [selectCell]);

    useEffect(() => {
        dispatch(
            fieldStateChange({
                fieldRect,
            }),
        );
    }, [fieldRect]);

    return selectCell ? <div className={Styles.cell_select} style={positionRect} /> : <></>;
};

export default CellSelect;
