import React, { CSSProperties, useEffect, useState } from 'react';
import { useSelector, useDispatch, Dispatch, connect } from 'react-redux';
import { cLog } from '@debug/logger';
import Styles from '@assets/scss/TableSheet.module.scss';

import { Fields, MouseEventMap, SheetModifyData, SheetOption, SheetViewPort, StoreState } from '@dataTypes';
import FieldView, { FIELD_TYPE } from './field-view';
import { SheetItem } from '@sheet/model';
import CellView from './cell-view';
import config from '@sheet/config';
import SheetUiContainer from '@sheet/container/sheet-ui-container';
import { getDevicePixelRatio } from '@sheet/utils';
import Scrollbar from '../common/scrollbar';

const { TABLE_SHEET_SIZE, SHEET_BOX_PADDING } = config.SHEET_TABLE;
const { BORDER_SIZE, CELL_WIDTH, TOP_FIELD_HEIGHT } = TABLE_SHEET_SIZE;
const { BOTTOM } = SHEET_BOX_PADDING;

export type ContCellGroupProps = {
    fields: Fields;
    modifyData: SheetModifyData;
    sheetItems: SheetItem[][];
    option?: SheetOption;
    cellHeight?: number;
    viewPort?: SheetViewPort;
    fieldMouseEventMap?: MouseEventMap;
    cellMouseEventMap?: MouseEventMap;
    updateTop?: (e) => void;
    updateLeft?: (e) => void;
};

class ContCellGroup extends React.Component<ContCellGroupProps> {
    state = {};

    contCellGroupRef: React.RefObject<HTMLDivElement>;
    cellGroupRef: React.RefObject<HTMLDivElement>;
    fieldViewRef: React.RefObject<typeof FieldView>;

    _rect: SheetViewPort;

    _isSynchronizationTop: boolean = false;

    constructor(props) {
        super(props);

        this.contCellGroupRef = React.createRef();
        this.cellGroupRef = React.createRef();
        this.fieldViewRef = React.createRef();

        this._rect = { x: 0, y: 0, width: 0, height: 0 };
    }

    componentWillUnmount() {
        cLog('[ContCellGroup] componentWillUnmount');
        this.contCellGroupRef.current?.removeEventListener('scroll', this._onScroll);
        this.cellGroupRef.current?.removeEventListener('scroll', this._onScroll);
    }

    componentDidMount() {
        cLog('[ContCellGroup] componentDidMount');
        this.contCellGroupRef.current?.addEventListener('scroll', this._onScroll);
        this.cellGroupRef.current?.addEventListener('scroll', this._onScroll);

        this.synchronizationTop(0, null);
    }

    componentDidUpdate() {
        cLog('[ContCellGroup] componentDidUpdate');
    }

    synchronizationTop = (top, target) => {
        if (!this.cellGroupRef.current) return;
        if (!target || this.cellGroupRef.current !== target) {
            this._isSynchronizationTop = true;
            this.cellGroupRef.current.scrollTop = top;
        }
    };

    synchronizationScroll = (left, top) => {
        setTimeout(() => {
            if (!this.cellGroupRef.current || !this.contCellGroupRef.current) return;
            this._isSynchronizationTop = true;
            this.cellGroupRef.current.scrollTop = top;
            this.contCellGroupRef.current.scrollLeft = left;
        }, 0);
    };

    getRect = () => {
        this._rect.x = this.contCellGroupRef.current?.scrollLeft || 0;
        this._rect.y = this.cellGroupRef.current?.scrollTop || 0;
        this._rect.width = this.contCellGroupRef.current?.clientWidth || 0;
        this._rect.height = this.cellGroupRef.current?.clientHeight || 0;
        return this._rect;
    };

    updateDevicePixelRatio = () => {
        const { fields } = this.props;
        const { rows } = fields || {};
        const width = BORDER_SIZE + CELL_WIDTH * (rows?.length || 1) + getDevicePixelRatio();
        if (this.cellGroupRef.current?.style) this.cellGroupRef.current.style.width = `${width}px`;

        this.fieldViewRef.current?.updateDevicePixelRatio();
    };

    _onScroll = (e) => {
        cLog('[ContCellGroup] _onScroll', this._isSynchronizationTop);
        if (this._isSynchronizationTop) {
            this._isSynchronizationTop = false;
            return;
        }

        const { updateTop, updateLeft } = this.props;
        const target = e.target;

        if (target === this.cellGroupRef.current) {
            cLog('[ContCellGroup] updateTop');
            if (updateTop) updateTop(e);
        } else {
            if (updateLeft) updateLeft(e);
        }
    };

    render() {
        const {
            fields,
            modifyData,
            viewPort,
            fieldMouseEventMap,
            cellMouseEventMap,
            sheetItems,
            cellHeight,
            option,
        } = this.props;
        const { rows } = fields || {};

        const width = BORDER_SIZE + CELL_WIDTH * (rows?.length || 1) + getDevicePixelRatio();
        const style: CSSProperties = { width };
        const cellGroupStyle: any = { width, overflowY: 'overlay' };
        const cellStyle: CSSProperties = { height: cellHeight };

        const { viewPortMaxSize } = viewPort || {};
        const { height } = viewPortMaxSize || {};
        let contCellGroupStyle: any = { overflowX: 'overlay' };

        if (cellHeight && height && height > cellHeight) {
            contCellGroupStyle = { ...contCellGroupStyle, height: cellHeight + TOP_FIELD_HEIGHT + 16 };
            cellGroupStyle.overflow = 'hidden';
        }
        return (
            <div className={Styles.cont_cell_group} ref={this.contCellGroupRef} style={contCellGroupStyle}>
                <FieldView
                    ref={this.fieldViewRef}
                    addStyle={style}
                    className={Styles.top_cell}
                    type={FIELD_TYPE.TOP}
                    fieldDatas={rows}
                    viewPort={viewPort}
                    modifyData={modifyData}
                    mouseEventMap={fieldMouseEventMap}
                />
                <Scrollbar className={Styles.cell_group} style={cellGroupStyle} ref={this.cellGroupRef}>
                    <CellView
                        addStyle={cellStyle}
                        viewPort={viewPort}
                        sheetItems={sheetItems}
                        modifyData={modifyData}
                        mouseEventMap={cellMouseEventMap}
                    />
                    <SheetUiContainer option={option} />
                </Scrollbar>
            </div>
        );
    }
}

const mapStateToProps = (_state: StoreState) => ({
    // viewPort: state.editor.viewPort,
});

const mapDispatchToProps = (_dispatch: Dispatch) => ({
    // selectCell: (selectCellItem) => dispatch(selectCell(selectCellItem)),
});

export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(ContCellGroup);
