export const getDevicePixelRatio = () => {
    let devicePixelRatioMargin = 0;

    if (window.devicePixelRatio <= 0.25) {
        devicePixelRatioMargin = 3;
    } else if (window.devicePixelRatio <= 0.34) {
        devicePixelRatioMargin = 2;
    } else if (window.devicePixelRatio < 1) {
        devicePixelRatioMargin = 1;
    }

    return devicePixelRatioMargin;
};

export const copyRectData = () => {
    const rect = StoreManager.state.sheet.fieldRect;
    const sheetItems = StoreManager.state.sheet.sheetItems;
    if (!(rect && sheetItems)) {
        return;
    }
    const rectData: string[] = [];
    const { sr, sc, er, ec } = rect;
    for (let i = sc; i <= ec; i++) {
        const colData: string[] = [];
        for (let j = sr; j <= er; j++) {
            colData.push(String(sheetItems[i][j]?.str) || '');
        }
        rectData.push(colData.join('\t'));
    }
    const text = rectData.join('\n');
    if (navigator.clipboard) {
        navigator.clipboard.writeText(text);
    }
};

export const clearRectData = () => {
    if (navigator.clipboard) {
        navigator.clipboard.writeText('');
    }
};

export const getClipboardData = async () => {
    if (!navigator.clipboard && !StoreManager.state.sheet.fieldRect) {
        return;
    }

    const text = await navigator.clipboard.readText();
    const isPasteData = isSpreadsheetData(text);

    if (!isPasteData || !text.length) {
        return;
    }

    let modifyData: ModifyData | null = new ModifyData();
    modifyData.type = MODIFY_TYPE.PASTE;
    modifyData.text = text;
    const { rect, selectCell } = StoreManager.state.editor;

    if (rect) {
        const { sc, ec, sr, er } = rect;
        modifyData.start = { col: sc, row: sr };
        modifyData.end = { col: ec, row: er };
    } else if (selectCell) {
        modifyData.start = selectCell;
        modifyData.end = selectCell;
    } else {
        modifyData = null;
    }

    return modifyData;
};
